<template>
  <ValidationProvider
    :rules="validationRules"
    :name="feedbackId"
    v-slot="vContext"
  >
    <div class="counter-input-contianer">
      <button
        type="button"
        @click="decrement"
        :class="{ 'counter-disabled': isDecrementDisabled }"
      >
        <img src="@/assets/images/icons/minus-circle.svg" alt="" />
      </button>
      <b-form-input
        type="number"
        class="counter-value"
        v-model.number="counterValue"
        @change="onInputChange"
        :state="getValidationState(vContext)"
        :aria-describedby="feedbackId"
        :value="value"
        :min="min"
        :max="max"
      ></b-form-input>

      <button
        type="button"
        @click="increment"
        :class="{ 'counter-disabled': isIncrementDisabled }"
      >
        <img src="@/assets/images/icons/plus-circle.svg" alt="photo" />
      </button>
    </div>
    <transition name="slide">
      <b-form-invalid-feedback :id="feedbackId" class="error-field">{{
        !vContext.valid && vContext.errors[0]
      }}</b-form-invalid-feedback>
    </transition>
  </ValidationProvider>
</template>

<script>
// TODO : this component has to be replaced with the bootstrap spin button
export default {
  name: 'CounterInput',
  inheritAttrs: false,
  props: {
    value: {
      // this prop for v-model
      required: true,
      type: Number,
    },
    min: {
      required: false,
      type: Number,
      default: 0,
    },
    max: {
      required: false,
      type: Number,
      default: 5000,
    },
    validationRules: {
      type: String,
      default: '',
    },
    step: {
      required: false,
      default: 1,
    },
    validationName: {
      type: String,
      required: true,
      default: 'text',
    },
  },
  data() {
    return {
      counterValue: 0,
    }
  },
  created() {
    this.counterValue = this.value
  },
  computed: {
    isIncrementDisabled() {
      return this.value == this.max
    },
    isDecrementDisabled() {
      return this.value <= this.min
    },
    feedbackId() {
      return 'input' + this.validationName.replace(/ /g, '-') + '-feedback'
    },
  },
  methods: {
    increment() {
      if (this.max && this.value == this.max) return
      this.emitInputEvent(+this.value + this.step)
    },
    decrement() {
      if (this.value > this.min) {
        this.emitInputEvent(+this.value - this.step)
      }
    },
    emitInputEvent(value) {
      this.$emit('input', value)
    },
    validateNumber(newValue) {
      return newValue >= this.min && newValue <= this.max ? true : false
    },
    getValidationState(vContext) {
      return vContext.errors[0] ? false : vContext.valid ? true : null
    },
    onInputChange() {
      this.validateNumber(this.counterValue)
        ? this.emitInputEvent(this.counterValue)
        : this.updateCounterValue(this.value)
    },
    updateCounterValue() {
      this.counterValue = this.value
    },
    getValidationState(vContext) {
      return vContext.errors[0] ? false : vContext.valid ? true : null
    },
  },
  mouted() {
    this.updateCounterValue()
  },
  watch: {
    value() {
      this.updateCounterValue()
    },
  },
}
</script>

<style scoped>
.counter-input-contianer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
button {
  background: none;
  border: none;
  width: 24px;
  padding: 0;
  cursor: pointer;
}
button.counter-disabled {
  cursor: default;
  opacity: 0.4;
}
.counter-value {
  width: auto;
  min-width: 80px;
  height: auto;
  display: inline-block;
  text-align: center;
  font-size: 14px;
  border: none;
  padding: 0;
  -webkit-appearance: textfield;
  -moz-appearance: textfield;
  appearance: textfield;
}
/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
</style>
