import API from './api';

export default {
  getMyProperties({ state, commit }) {
    commit('updateMyPropertiesLoading', true);
    return API.getMyPropertiesOffers({ ...state.myProperties.params })
      .then((res) => {
        commit('setMyPropertiesData', {
          data: res.Data.data,
          total: res.Data.total,
        });
      })
      .finally(() => {
        commit('updateMyPropertiesLoading', false);
      });
  },
  updateMyPropertiesParams({ commit, dispatch }, params) {
    commit('updateMyPropertiesParams', params);
    return dispatch('getMyProperties');
  },
  getMyPropertiesPage({ state, commit }) {
    //my-properties
    commit('updateMyPropertiesPageLoading', true);
    return API.getMyPropertiesPage({ ...state.myPropertiesPage.params })
      .then((res) => {
        commit('setMyPropertiesPageData', {
          data: res.Data.data,
          total: res.Data.total,
        });
      })
      .finally(() => {
        commit('updateMyPropertiesPageLoading', false);
      });
  },
  updateMyPropertiesPageParams({ commit, dispatch }, params) {
    commit('updateMyPropertiesPageParams', params);
    return dispatch('getMyPropertiesPage');
  },
  getPropertyOffers({}, payload) {
    return API.getPropertyOffers(payload);
  },
  getAllMyProperties() {
    return API.getAllMyProperties();
  },
  getSentOffersProperties({ state, commit }) {
    commit('updateSentOffersPropertiesLoading', true);
    return API.getSentOffersProperties(state.sentOffersProperties.params)
      .then((res) => {
        commit('setSentOffersPropertiesData', {
          data: res.Data.data,
          total: res.Data.total,
        });
      })
      .finally(() => {
        commit('updateSentOffersPropertiesLoading', false);
      });
  },
  updateSentOffersPropertiesParams({ commit, dispatch }, params) {
    commit('updateSentOffersPropertiesParams', params);
    return dispatch('getSentOffersProperties');
  },
  getSentOffers({}, params) {
    return API.getSentOffers(params);
  },
  getSentOffersForFilter() {
    return API.getSentOffersForFilter();
  },
  getFavourites({ state, commit }) {
    commit('updateMyFavouritesLoading', true);
    return API.getFavourites(state.myFavourites.params)
      .then((res) => {
        commit('setFavourites', {
          data: res.collection,
          total: res.total,
        });
      })
      .finally(() => {
        commit('updateMyFavouritesLoading', false);
      });
  },
  getSimilarProperties({ state, commit }, payload) {
    commit('updateSimilarPropertiesLoading', true);

    return API.getSimilarProperties(payload.property_id, payload.property_slug)
      .then((res) => {
        commit('setSimilarProperties', {
          data: res.Data,
        });
      })
      .finally(() => {
        commit('updateSimilarPropertiesLoading', false);
      });
  },
  getSinglePropertyDetails({ commit }, payload) {
    return API.getSinglePropertyDetails(
      payload.property_slug,
      payload.term
    ).then((res) => {
      commit('setPropertyDetails', res.Data);
    });
  },
  getPropertyReviews({ commit }, payload) {
    commit('updatePropertyReviewLoading', true);
    return API.getPropertyReviews(payload.property_id, payload.per_page).then(
      (res) => {
        commit('setPropertyReviews', res.Data);
        commit('updatePropertyReviewLoading', false);
      }
    );
  },
  // getPropertyQuestions({ commit }, payload) {
  //   commit('setReviewQuestions', {})
  //   commit('updateReviewModalLoading', true)
  //   return API.getPropertyQuestions(payload.property_id)
  //     .then((res) => {
  //       commit('setPropertyQuestions', res.Data)
  //       commit('setReviewQuestions', res.Data)
  //     })
  //     .finally(() => {
  //       commit('updateReviewModalLoading', false)
  //     })
  // },
  // getNeighbourQuestions({ commit }, payload) {
  //   return API.getNeighbourQuestions(payload.property_id).then((res) => {
  //     commit('setNeighbourQuestions', res.Data)
  //   })
  // },
  async loadPropertyData({ dispatch, state, commit }, payload) {
    if (state.term == null) {
      commit('setTerm', payload.term);
    }
    await dispatch('getSinglePropertyDetails', {
      property_slug: payload.propertySlug,
      term: state.term,
    });
  },
  updatePropertyFavourite({ commit, rootState, dispatch }, payload) {
    if (!rootState.UserModule.user) {
      dispatch(
        'AuthModule/showAuthModal',
        {
          callbackAction: {
            name: 'PropertyModule/updatePropertyFavourite',
            payload,
          },
        },
        {
          root: true,
        }
      );
      return;
    } else {
      return API.updatePropertyFavourite(payload)
        .then((res) => {
          commit('updateIsFavourite', payload);
        })
        .catch((error) => {
          if (error.response && error.response.data) {
            let errorMessage = error.response.data.Data.error.message;
            this._vm.$bvToast.toast(`${errorMessage} `, {
              title: 'Error',
              autoHideDelay: 5000,
              variant: 'danger',
              appendToast: true,
            });
          }
        });
    }
  },
  removePropertyFavourite({ commit, rootState, dispatch }, payload) {
    if (!rootState.UserModule.user) {
      dispatch(
        'AuthModule/showAuthModal',
        {
          callbackAction: {
            name: 'PropertyModule/removePropertyFavourite',
            payload,
          },
        },
        {
          root: true,
        }
      );
      return;
    } else {
      return API.updatePropertyFavourite(payload).then((res) => {
        dispatch('getFavourites');
      });
    }
  },
  sendOffer({ commit, rootState, dispatch }, payload) {
    if (!rootState.UserModule.user) {
      dispatch(
        'AuthModule/showAuthModal',
        {
          callbackAction: {
            name: 'PropertyModule/sendOffer',
            payload,
          },
        },
        {
          root: true,
        }
      );
      return;
    } else {
      return API.sendOffer(payload)
        .then((res) => {
          commit('updateSuccessModal', {
            successModalVisibility: true,
          });
        })
        .catch((error) => {
          commit('setServerErrors', error.response ? error.response.data : {});
          if (error.response && error.response.data.original) {
            let errorMessage = error.response.data.original.Data.message;
            this._vm.$bvToast.toast(`${errorMessage} `, {
              title: 'Error',
              autoHideDelay: 5000,
              variant: 'danger',
              appendToast: true,
            });
          }
        });
    }
  },
  showSendOfferModal({ commit, rootState, dispatch }, payload) {
    commit('setServerErrors', null);
    commit('updateSendOfferModalVisibility', {
      visibility: true,
      data: payload.offerData,
    });
  },
  showConfirmationModal({ commit, rootState, dispatch }, payload) {
    commit('setServerErrors', null);
    commit('updateConfirmationModalVisibility', {
      visibility: true,
      data: payload.confirmData,
    });
  },
  showSuccessModal({ commit }) {
    commit('updateSuccessModal', {
      successModalVisibility: true,
    });
  },
  updateMyFavouritesParams({ commit, dispatch }, params) {
    commit('updateMyFavouritesParams', params);
    return dispatch('getFavourites');
  },

  hideSendOfferModal({ commit }) {
    commit('updateSendOfferModalVisibility', {
      visibility: false,
      data: {},
    });
  },
  hideConfirmationModal({ commit }) {
    commit('updateConfirmationModalVisibility', {
      visibility: false,
      data: {
        form: {},
        offerInfo: {},
      },
    });
  },
  hideSuccessModal({ commit }) {
    commit('updateSuccessModal', {
      successModalVisibility: false,
    });
  },
  getSingleOffer({ commit, dispatch, state }, offerId) {
    commit('updateOfferDetailsLoading', true);
    return API.getSingleOffer(offerId)
      .then((res) => {
        commit('setSingleOfferDetails', res.Data);
        commit('setSelectedOfferId', offerId);
        commit('updateOfferStatus', res.Data.status);
        commit('updateRejectionReason', res.Data.rejection_reason);

        dispatch('getTenantReviewNotes', {
          tenant_id: res.Data.tenant_id,
          per_page: state.numOftenantReviewsShown,
        });
      })
      .catch(() => {
        commit('setSingleOfferDetails', {});
      })
      .finally(() => {
        commit('updateOfferDetailsLoading', false);
      });
  },
  getTenantReviewNotes({}, payload) {
    return new Promise((resolve, reject) => {
      API.getTenantReviewNotes(payload)
        .then((res) => {
          resolve(res);
        })
        .catch((error) => {
          reject(error);
        });
    });
  },
  getRejectionReasons({ commit }) {
    return API.getRejectionReasons().then((res) => {
      commit('setRejectionReasons', res.Data);
    });
  },
  getSingleOfferDetails({ dispatch }, offerId) {
    return Promise.all([
      dispatch('getSingleOffer', offerId),
      // dispatch('getRejectionReasons'),
    ]);
  },
  acceptRejectOffer({ commit, state }, payload) {
    commit('updateRespondActionLoading', {
      type: payload.type,
      status: true,
    });
    commit('updateRespondActionVisibility', {
      type: payload.type,
      visibility: true,
    });
    if (payload.type == 'reject') {
      commit('updateRejectionReason', payload.rejection_reason);
    }
    return new Promise((resolve, reject) => {
      API.acceptRejectOffer(payload)
        .then((res) => {
          commit('updateOfferStatus', res.Data.type);
          commit('updateRespondActionVisibility', {
            type: payload.type,
            visibility: false,
          });
          if (res.Data.type == 1) {
            this._vm.$bvToast.toast(`${this.$i18n.t('form.accept_offer')} `, {
              variant: 'success',
              toaster: 'b-toaster-top-center',
              solid: true,
              bodyClass: ['toast-body'],
              headerClass: ['toast-header'],
              noCloseButton: true,
            });
          }
          if (res.Data.type == 2) {
            const message = state.activeFriend.user_name
              ? `${this.$i18n.t('form.sent_email_to')} ${
                  state.activeFriend.user_name
                }`
              : this.$i18n.t('form.counter_offer_sent_successfully');
            this._vm.$bvToast.toast(message, {
              variant: 'success',
              toaster: 'b-toaster-top-center',
              solid: true,
              bodyClass: ['toast-body'],
              headerClass: ['toast-header'],
              noCloseButton: true,
            });
          }
          if (res.Data.type == 3) {
            this._vm.$bvToast.toast(`${this.$i18n.t('form.rejected_offer')} `, {
              variant: 'warning',
              toaster: 'b-toaster-top-center',
              solid: true,
              bodyClass: ['toast-body'],
              headerClass: ['toast-header'],
              noCloseButton: true,
            });
          }
          resolve(res);
        })
        .catch((error) => {
          if (error.response && error.response.data.original) {
            let errorMessage = '';
            // this is temporary error handle
            if (error.response.data.original.Data[0].errorMsg) {
              errorMessage = error.response.data.original.Data[0].errorMsg;
            }
            if (error.response.data.original.Data.message) {
              errorMessage = error.response.data.original.Data.message;
            }
            this._vm.$bvToast.toast(`${errorMessage} `, {
              title: 'Error',
              autoHideDelay: 5000,
              variant: 'danger',
              appendToast: true,
            });
          }
          reject(error);
        })
        .finally(() => {
          commit('updateRespondActionLoading', {
            type: '',
            status: false,
          });
        });
    });
  },
  getPropMasterData({ commit }) {
    return API.getPropMasterData().then((res) => {
      commit('updatePropMasterData', res);
    });
  },
  getHotelType({ commit }) {
    return API.getHotelType().then((res) => {
      commit('updateHotelType', res);
    });
  },
  getChildRoot({ commit }, payload) {
    return API.getChildRoot(payload.location_id).then((res) => {
      commit('updateChildRoot', { response: res, type: payload.type });
    });
  },
  changeIndicator({ commit }, payload) {
    commit('updateIndicator', payload);
  },
  changeSort({ commit }, payload) {
    commit('changeSortValue', payload);
  },
  openComapreModal({ commit }, payload) {
    commit('activeCmpareModal', payload);
  },
  getCompareProperty({ commit }, payload) {
    return API.compareProperty(payload).then((res) => {
      commit('listCompareProperty', res.properties);
    });
  },
  clearCompareProperty({ commit }) {
    commit('clearCompareProperty');
  },
  addProperty({ dispatch, rootState, commit }, payload) {
    if (!rootState.UserModule.user) {
      dispatch(
        'AuthModule/showAuthModal',
        {
          callbackAction: {
            name: 'PropertyModule/addProperty',
            payload,
          },
        },
        {
          root: true,
        }
      );
      return;
    } else {
      return API.addProperty(payload)
        .then((res) => {
          localStorage.setItem('createdPropertyId', res.Data.data.id);
          commit('set_createdPropertyId', res.Data.data.id);
          if(res.Status === 200){
            let isOwnerData = localStorage.getItem('property') && JSON.parse(localStorage.getItem('property'));
            if(!!isOwnerData){
              let ownerData = JSON.parse(localStorage.getItem('property')).form6;
              ownerData.property_id = parseInt(localStorage.getItem('createdPropertyId')); // res.Data.data.id
              this.$axios.$post('/user/owners/create', ownerData)
                .then(res => {
                  let ownerId = res.Data.id;
                  this.$axios.$post(`/user/owners/attach/${parseInt(localStorage.getItem('createdPropertyId'))}/owner/${ownerId}`);
                })
            }
          }
          this._vm.$bvToast.toast(this.$i18n.t('form.successList'), {
            title: this.$i18n.t('form.congratulations'),
            variant: 'primary',
            toaster: 'b-toaster-top-center',
            solid: true,
            autoHideDelay: 1500,
            noCloseButton: true,
          });
          commit('SET_PROPERTY_IMAGES', []);
          // commit('set_createdPropertyId_Null');
          localStorage.removeItem('property');
          // localStorage.removeItem('createdPropertyId');
          this.app.router.replace(this.localePath('/dashboard/my-properties'));
        })
        .catch((error) => {
          let errorMessage = '';
          if (error.response && error.response.data) {
            errorMessage = error.response?.data?.Message;
          } else if (error.response && error.response.message) {
            errorMessage = error.response.message;
          }
          errorMessage &&
            this._vm.$bvToast.toast(`${errorMessage} `, {
              title: 'Error',
              autoHideDelay: 5000,
              variant: 'danger',
              appendToast: true,
            });
        });
    }
  },
  async postRequest({ dispatch, rootState, commit }, payload) {
    if (!rootState.UserModule.user) {
      dispatch(
        'AuthModule/showAuthModal',
        {
          callbackAction: {
            name: 'PropertyModule/postRequest',
            payload,
          },
        },
        {
          root: true,
        }
      );
      return;
    }

    try {
      await API.postRequest(payload);

      this._vm.$bvToast.toast(this.$i18n.t('form.successRequest'), {
        title: this.$i18n.t('form.congratulations'),
        variant: 'primary',
        toaster: 'b-toaster-top-center',
        solid: true,
        autoHideDelay: 1500,
        noCloseButton: true,
      });

      commit('SET_POST_REQUEST_FORM_SUBMITTED');
      localStorage.removeItem('request');
      this.app.router.replace(this.localePath('/dashboard/requests'));
    } catch (error) {
      throw error;
    }
  },
  resetFilterData({ commit }) {
    commit('resetFilterData');
  },
  getSearchProperty({ commit, state }, payload) {
    commit('updatePropertySearchLoading', true);
    commit('updatePropertySearchParams', payload);
    return API.getProperty(state.filteredProperty.params)
      .then((res) => {
        commit('listSearchedProperty', res.Data);
        payload.term !== undefined && commit('setTerm', payload.term);
        commit('setRelated', res.Data.related);
        return res.Data.properties;
      })
      .finally(() => {
        commit('updatePropertySearchLoading', false);
      });
  },
  getSearchGraph({ commit, state }) {
    return API.getSearchGraph(state.filteredProperty.params);
  },
  getSimilarLocations({ commit }, location_id) {
    return API.getSimilarLocations(location_id).then((res) => {
      commit('setSimilarLocations', res.Data.locations);
    });
  },
  async getSearch({ dispatch }, payload) {
    await dispatch('getSearchProperty', payload).then((res) => {
      // if (res.length > 0) {
      //   return dispatch('getSimilarLocations', res[0].location_id)
      // }
    });
  },
  updatePropertyOpen({ commit }, payload) {
    return API.updatePropertyOpen(payload).then(() => {
      commit('updatePropertyOpen', payload);
    });
  },
  updateProperty(context, payload) {
    // Display the key/value pairs
    return API.updateProperty(payload)
      .then(() => {
        this._vm.$bvToast.toast(this.$i18n.t('form.successPropertyEdit'), {
          title: this.$i18n.t('form.congratulations'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
        });
        setTimeout(() => {
          this.app.router.push({
            path: this.localePath('/dashboard/my-properties'),
          });
        }, 500);
      })
      .catch((error) => {
        let errorMessage = '';
        if (error.response && error.response.data) {
          errorMessage = error.response.data.Data.error.message;
        } else if (error.response && error.response.message) {
          errorMessage = error.response.message;
        }
        errorMessage &&
          this._vm.$bvToast.toast(`${errorMessage} `, {
            title: 'Error',
            autoHideDelay: 5000,
            variant: 'danger',
            appendToast: true,
          });
      });
  },
  getOldPropertyData(context, payload) {
    return API.getOldPropertyData(payload).then((res) => {
      context.commit('listOldPropertyData', res.Data);
    });
  },
  changeOldPropertyImages({ commit }, payload) {
    commit('changeOldPropertyImages', payload);
  },
  getLandLordReviews({ commit }, payload) {
    commit('setLandLordReviews', payload);
  },
  showRatingModal({ commit }) {
    commit('updateAddEditReviewModalVisibility', true);
  },
  hideRatingModal({ commit }) {
    commit('updateAddEditReviewModalVisibility', false);
  },
  storeOrUpdateLandLordReviews({ commit }, payload) {
    commit('updateAddEditReviewButtonLoading', true);
    return new Promise((resolve, reject) => {
      API.storeOrUpdateLandLordReviews(payload)
        .then((res) => {
          commit('updateAddEditReviewModalVisibility', false);
          resolve(res);
        })
        .catch((error) => {
          if (error.response && error.response.data.original) {
            let errorMessage = '';
            if (error.response.data.original.Data.message) {
              errorMessage = error.response.data.original.Data.message;
            }
            this._vm.$bvToast.toast(`${errorMessage} `, {
              title: 'Error',
              autoHideDelay: 5000,
              variant: 'danger',
              appendToast: true,
            });
          }
          reject(error);
        })
        .finally(() => {
          commit('updateAddEditReviewButtonLoading', false);
        });
    });
  },
  setReviewsNote({ commit }, payload) {
    commit('setReviewsNote', payload);
  },
  setTenantReviews({ state, commit }, payload) {
    commit('resetTenantReviews');
    commit('setTenantReviews', payload.propertyReview);
    commit('setTenantReviews', payload.landlordReview);
    commit('setTenantReviews', payload.neighbourReview);
  },
  updateReviews({ commit }, payload) {
    commit('updateReviews', payload);
  },
  storeOrUpdateTenantReviews({ commit }, payload) {
    commit('updateAddEditReviewButtonLoading', true);
    return API.storeOrUpdateTenantReviews(payload)
      .then((res) => {
        commit('updateAddEditReviewModalVisibility', false);
        return res;
      })
      .catch((error) => {
        if (error.response && error.response.data.original) {
          let errorMessage = '';
          if (error.response.data.original.Data.message) {
            errorMessage = error.response.data.original.Data.message;
          }
          this._vm.$bvToast.toast(`${errorMessage} `, {
            title: 'Error',
            autoHideDelay: 5000,
            variant: 'danger',
            appendToast: true,
          });
        }
        return Promise.reject(error);
      })
      .finally(() => {
        commit('updateAddEditReviewButtonLoading', false);
      });
  },
  showReviewModal({ commit }) {
    commit('updateReviewModalVisibility', true);
  },
  hideReviewModal({ commit }) {
    commit('updateReviewModalVisibility', false);
  },
  getTenantReviews({ commit }, payload) {
    commit('setReviewQuestions', {});
    commit('updateReviewModalLoading', true);
    return API.getTenantReviews(payload.tenant_id)
      .then((res) => {
        commit('setReviewQuestions', res.Data);
      })
      .finally(() => {
        commit('updateReviewModalLoading', false);
      });
  },
  setActiveChatFriend({ commit }, payload) {
    commit('SET_ACTIVE_CHAT_FRIEND', payload);
  },
  setSortFilter({ commit }, newVal){
    commit('setSortFilter', newVal)
  },
  setSortedPropeties({ commit }, newVal){
    commit('SET_SORTED_PROPERTIES', newVal)
  },
  set_clickedPropToAttachOwner({commit}, prop){
    commit('set_clickedPropToAttachOwner', prop)
  }
};
