<template>
  <div>
    <b-modal
      ref="send-offer-modal"
      id="send-offer-modal"
      centered
      hide-header
      hide-footer
      @hidden="onCloseModal">
      <PropertyBid
        v-if="offerData"
        :property-price="offerData.monthly_price"
        :property-daily-price="offerData.daily_price"
        :property-security-deposite="offerData.property_security_deposit"
        :property-rate="offerData.property_rating"
        :property-reviews-Count="offerData.property_rating_count"
        :property-name="offerData.property_name"
        :property-term="term"
        :property-id="offerData.property_id"
        :offerPrice="offerData.offer_price"
        :isModal="true" />
    </b-modal>
  </div>
</template>

<script>
import PropertyBid from '~/components/property-details/PropertyBid';
import { createNamespacedHelpers } from 'vuex';

const { mapState, mapActions } = createNamespacedHelpers('PropertyModule');
export default {
  name: 'SendOfferModal',
  components: { PropertyBid },

  data() {
    return {};
  },
  computed: {
    ...mapState({
      visibility: (state) => state.sendOfferModal.visibility,
      offerData: (state) => state.sendOfferModal.data,
      termState: (state) => state.term,
    }),
    term() {
      let cardTerm = this.offerData.term;
      return this.$route.query.term ? this.$route.query.term : cardTerm;
    },
    isRTL() {
      return this.$store.getters['CoreModule/isRTL'];
    },
  },
  watch: {
    visibility(newVal) {
      newVal
        ? this.$refs['send-offer-modal'].show()
        : this.$refs['send-offer-modal'].hide();
    },
  },
  beforeDestroy() {
    this.hideSendOfferModal();
  },
  methods: {
    ...mapActions(['hideSendOfferModal']),
    onCloseModal() {
      this.hideSendOfferModal();
    },
  },
};
</script>

<style lang="scss" scoped>
::v-deep() {
  #send-offer-modal {
    .modal-dialog {
      max-width: 358px;
      margin: auto;
    }
    .modal-content {
      border-radius: 12px;
    }
    .modal-body {
      padding: 0;
    }
    &.modal {
      display: flex !important;
      height: 100%;
    }
  }
}
</style>
