<!-- menu actions icons and titles  -->
<template>
  <NuxtLink :to="localePath(item.link)" :class="`btn btn_${item.type}`">
    <!-- icons and title of each btn -->
    <div class="title">
      <!-- get icon and post it based on name of icon from /assets/images/sidebar  -->
      <img
        v-if="item.icon"
        width="22px"
        height="22px"
        :src="require(`@/assets/images/sidebar/${item.icon}`)" />
      <!-- get title and post it  -->
      <span :class="dir === 'rtl' ? 'mr-2' : 'ml-2'">{{ item.title }}</span>
    </div>
    <!--left arrow icon  -->
    <img
      v-if="dir === 'rtl'"
      width="16px"
      height="16px"
      class="arrow-icon"
      :src="require('@/assets/images/sidebar/CaretLeft.svg')"
      alt="photo" />
    <!--right arrow icon  -->
    <img
      v-else
      width="16px"
      height="16px"
      class="arrow-icon"
      :src="require('@/assets/images/sidebar/CaretRight.svg')"
      alt="photo" />
  </NuxtLink>
</template>

<script>
export default {
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  computed:{
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  }
};
</script>

<style lang="scss" scoped>
.btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  font-weight: bold;
  margin: 16px 0;
  padding: 12px;
  border-radius: 8px;

  &:last-child {
    margin-bottom: 0;
  }

  .title {
    display: flex;
    justify-content: center;
    align-items: center;

    // img {
    //   margin-right: 8px;
    // }
  }

  .arrow-icon {
    opacity: 0.7;
  }

  &_outlined {
    width: 100%;
    background-color: #fff;
    border: 1px solid #f2f4f5;

    span {
      color: #111;
    }
  }

  &_primary {
    background-color: #0078ff;
    color: #fff !important;
    border: none;

    .title {
      width: 100%;
    }
  }
}
</style>
