<template>
  <div class="d-md-none lowerNav bg-white fixed-bottom" v-if="!hide">
    <b-container fluid>
      <div class="icons-nav d-flex justify-content-between">
        <div class="icon d-flex flex-column justify-content-center align-items-center icon-home" @click="goToHomePage">
          <img src="@/assets/images/home/lowerNav/HouseLine.svg">
          <p class="small text-center">Home</p>
        </div>
        <div v-if="theView === 'tenantView'" class="icon d-flex flex-column justify-content-center align-items-center icon-search" @click="() => this.$router.push(this.localePath('/search'))">
          <img src="@/assets/images/home/lowerNav/MagnifyingGlass.svg">
          <p class="small text-center">Search</p>
        </div>
        <div v-else class="icon d-flex flex-column justify-content-center align-items-center icon-search" @click="() => this.$router.push(this.localePath('/marketplace'))">
          <img src="@/assets/images/home/lowerNav/Storefront.svg">
          <p class="small text-center">Market</p>
        </div>
        <div v-if="theView === 'tenantView'" class="icon d-flex flex-column justify-content-center align-items-center icon-plus" @click="() => this.$router.push(this.localePath('/requests/post/property-category'))">
          <img src="@/assets/images/home/lowerNav/PlusCircle.svg">
          <p class="small text-center">Post Request</p>
        </div>
        <div v-else class="icon d-flex flex-column justify-content-center align-items-center icon-plus" @click="() => this.$router.push(this.localePath('/properties/add/1'))">
          <img src="@/assets/images/home/lowerNav/PlusCircle.svg">
          <p class="small text-center">Add Unit</p>
        </div>
        <div v-if="theView === 'tenantView' && user" class="icon d-flex flex-column justify-content-center align-items-center" @click="() => this.$router.push(this.localePath('/dashboard/requests'))">
          <img src="@/assets/images/home/lowerNav/my_requests.svg">
          <p class="small text-center">My Requests</p>
        </div>
        <div v-if="theView === 'ownerView' && user" class="icon d-flex flex-column justify-content-center align-items-center" @click="() => this.$router.push(this.localePath('/dashboard/offers?type=public&page'))">
          <img src="@/assets/images/home/lowerNav/my_offers.svg">
          <p class="small text-center">My Offers</p>
        </div>
        <div v-if="!user" class="icon d-flex flex-column justify-content-center align-items-center" @click="showAuthModal">
          <img src="@/assets/images/home/lowerNav/account.svg">
          <p class="small text-center">My Account</p>
        </div>
        <div class="icon d-flex flex-column justify-content-center align-items-center icon-menu" @click="showSideBar = !showSideBar">
          <img src="@/assets/images/home/lowerNav/List.svg">
          <p class="small text-center">Menu</p>
        </div>
      </div>
    </b-container>
    <SideBar @hideSideBar="showSideBar = false" :showSideBar="showSideBar" />
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
const { mapState, mapActions } = createNamespacedHelpers('HomeModule');
const { mapState: userMapState } = createNamespacedHelpers('UserModule');
const { mapActions: mapAuthActions } = createNamespacedHelpers('AuthModule');

export default {
  name: "LowerNav",
  data(){
    return {
      showSideBar: false,
      hide: false,
    }
  },
  components:{
    SideBar: () => import('@/components/shared/AppHeader/SideBar.vue'),
  },
  computed: {
    ...mapState(['theView']),
    ...userMapState(['user']),
  },
  methods:{
    ...mapActions(['Set_View']),
    ...mapAuthActions(['showAuthModal']),
    goToHomePage(){
      this.$router.push(this.localePath('/'));
      this.Set_View(localStorage.getItem('choosedUserView'))
    }
  },
  watch: {
    $route: {
      handler(value) {
        let landlordRoutes = ['marketplace', 'my-properties', 'dashboard/offers', '/owner'];
        let tenantRoutes = ['search', 'favorite-properties', 'dashboard/requests'];
        for(let landlordRoute of landlordRoutes){
          if(value.path.indexOf(landlordRoute) >= 0){
            this.Set_View('ownerView');
          }
        }
        for(let tenantRoute of tenantRoutes){
          if(value.path.indexOf(tenantRoute) >= 0){
            this.Set_View('tenantView');
          }
        }
        if(value.path.indexOf('properties/add') >= 0|| value.path.indexOf('requests/post') >= 0){
          // alert('Exist');
          this.hide = true;
        }else{
          this.hide = false;
        }
      },
      immediate: true,
    },
  },
}
</script>

<style lang="scss">
.icons-nav {
  z-index: 5; 
  .icon {
    flex-basis: 20% !important;
    p {
      font-size: 0.75em;
    }
    &.icon-plus {
      transform: translateY(-15px);
    }
  }
}
</style>