<template>
  <div>
    <div>
      <Nuxt />
    </div>
    <noscript v-if="this.$config.APP_ENV == 'production'"><iframe
        src="https://www.googletagmanager.com/ns.html?id=GTM-5Z3VS3H" height="0" width="0"
        style="display: none; visibility: hidden"></iframe></noscript>
  </div>
</template>

<script>
import AuthModal from '@/components/shared/AuthModal'

export default {
  name: 'PropertiesLayout',

  head() {
    return this.$nuxtI18nHead({ addSeoAttributes: true })
  },
  data() {
    return {
      scrollEventDebounce: null,
      isTransparentHeader: true,
      scrollEventActivated: false,
    }
  },
  computed: {
    isHomePage() {
      return this.$route?.name?.includes('index')
    },
    isPropertyListing() {
      return this.$route?.name?.includes('properties-add')
    },
    userID() {
      return this.$store.state.UserModule.user
    },
  },
  methods: {
    handleScrollEvent() {
      clearTimeout(this.scrollEventDebounce)
      this.scrollEventDebounce = setTimeout(() => {
        if (window.scrollY > 100) {
          this.isTransparentHeader = false
        } else {
          this.isTransparentHeader = true
        }
      }, 150)
    },
    addScrollEvent() {
      this.isTransparentHeader = true

      window.addEventListener('scroll', this.handleScrollEvent)
    },
    removeScrollEvent() {
      window.removeEventListener('scroll', this.handleScrollEvent)
    },
    handleOnScroll(path) {
      if (typeof window === 'undefined') return
      if (this.isHomePage) {
        !this.scrollEventActivated && this.addScrollEvent()
      } else {
        this.removeScrollEvent()
      }
    },
  },
  mounted() {
    this.handleOnScroll(this.$route.path)
    // this.handleNotification()
    // this.$store.dispatch('UserModule/getNotifcation')
  },
  watch: {
    $route(to) {
      this.handleOnScroll(to.path)
    },
    $i18n: {
      handler(value) {
        if (process.client) {
          if (this.$i18n.locale === 'ar') {
            document.body.lang = 'ar'
            document.body.dir = 'rtl'
          } else {
            document.body.lang = 'en'
            document.body.dir = 'ltr'
          }
        }
      },
      immediate: true,
    },
  },
  head() {
    return {
      ...this.$nuxtI18nHead({ addSeoAttributes: true }),
      htmlAttrs: {
        lang: this.$i18n.locale,
      },
      meta: [

      ],
      script: [
        this.$config.APP_ENV == 'production' ? {
          hid: 'google-manager',
          type: 'text/javascript',
          innerHTML:
            "(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src='https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);})(window,document,'script','dataLayer','GTM-5Z3VS3H');",
        } : {},
        {
          hid: 'facebook-sdk',
          type: 'text/javascript',
          innerHTML: `
           window.fbAsyncInit = function () {
        FB.init({
          cookie: true,
          xfbml: true,
          version: 'v10.0',
        })

        FB.AppEvents.logPageView()
      }

      ;(function (d, s, id) {
        var js,
          fjs = d.getElementsByTagName(s)[0]
        if (d.getElementById(id)) {
          return
        }
        js = d.createElement(s)
        js.id = id
        js.src = 'https://connect.facebook.net/en_US/sdk.js'
        fjs.parentNode.insertBefore(js, fjs)
      })(document, 'script', 'facebook-jssdk')
           `,
        },
      ],
      __dangerouslyDisableSanitizersByTagID: {
        'google-manager': ['innerHTML'],
        'google-analytics': ['innerHTML'],
        'facebook-sdk': ['innerHTML'],
      },
    }
  },
}
</script>

<style lang="scss" scoped>
.main-content-container {
  min-height: calc(100vh - 274px);
  /* fix footer issue with pages that not have enough content */
  padding-top: 48px;

  .no-padding-top & {
    padding-top: 0;
  }
}
</style>
