<template>
  <div class="social-login-container">
    <no-ssr>
      <GoogleLogin
        type="button"
        class="btn social-login-button btn-outline-dark btn-block"
        :params="googleParams"
        :onSuccess="onGoogleLoginSuccess"
        :onFailure="onGoogleLoginFailure"
        :id="createId"
      >
        <b-img src="@/assets/images/icons/google-icon.svg" alt="Google Login" />
        <span class="button-text">
          {{ $t('form.continueWithGoogle') }}
        </span>
      </GoogleLogin>
    </no-ssr>
    
    <!-- <b-button
      class="social-login-button"
      variant="outline-dark"
      block
      @click.prevent="facebookLogin"
    >
      <b-img
        src="@/assets/images/icons/facebook-icon.svg"
        alt="Facebook Login"
      />
      <span class="button-text">
        {{ $t('form.continueWithFacebook') }}
      </span>
    </b-button> -->

    <!-- <no-ssr>
      <FacebookLogin
        v-show="dialog"
        ref="fb"
        class="button fb"
        version="v10.0"
        :loginOptions="facebookLoginOptions"
        @login="onFacebookLogin"
        @logout="onFacebookLogout"
        @sdk-loaded="sdkLoaded"
        @get-initial-status="getFacebookUserData"
      ></FacebookLogin>
    </no-ssr> -->
  </div>
</template>

<script>
import FacebookLogin from 'facebook-login-vuejs'
import GoogleLogin from 'vue-google-login'

import { createNamespacedHelpers } from 'vuex'
const { mapActions } = createNamespacedHelpers('AuthModule')
export default {
  name: 'Social',

  components: {
    FacebookLogin,
    GoogleLogin,
  },
  props: {
    needReload: {
      required: true,
      type: Boolean,
    },
    dialog: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      isLoggedIn: false,
      show: false,
      isFacebookConnected: false,
      FB: null,
      facebookLoginOptions: {
        scope: 'public_profile,email',
      },
      googleParams: {
        client_id:
          '961884146363-us29u76i5fqc9a7afb75et4g00jqmo5d.apps.googleusercontent.com',
        client_secret: 'yK6KA3IWBgjQgBikrAsnnTCl',
      },
    }
  },
  methods: {
    ...mapActions(['createAccount']),
    facebookLogin() {
      FB.login((res) => {
        const {
          authResponse: { accessToken },
        } = res

        this.getFacebookUserData(accessToken)
      })
      // FB.getLoginStatus((res) => {
      // })
      // this.isLoggedIn = true
      // this.$nextTick(() => {
      //   this.$refs.fb.login()
      // })
    },
    getFacebookUserData(accessToken) {
      // if (!this.FB) return
      // let accessToken = this.FB.getAccessToken()

      // if (this.isLoggedIn) {
      this.createAccount({
        payload: {
          provider: 'facebook',
          provider_id: false,
          provider_token: accessToken,
        },
        needReload: this.needReload,
      })
      // }
    },
    // sdkLoaded(payload) {
    //   this.isFacebookConnected = payload.isConnected
    //   this.FB = payload.FB
    //   if (this.isFacebookConnected) this.getFacebookUserData()
    // },
    // onFacebookLogin() {
    //   this.isFacebookConnected = true
    //   this.getFacebookUserData()
    // },
    // onFacebookLogout() {
    //   this.isFacebookConnected = false
    // },
    findValues(obj, key){
      return this.findValuesHelper(obj, key, []);
    },

    findValuesHelper(obj, key, list) {
      if (!obj) return list;
      if (obj instanceof Array) {
        for (var i in obj) {
          list = list.concat(this.findValuesHelper(obj[i], key, []));
        }
        return list;
      }
      if (obj[key]) list.push(obj[key]);

      if ((typeof obj == "object") && (obj !== null) ){
        var children = Object.keys(obj);
        if (children.length > 0){
          for (i = 0; i < children.length; i++ ){
            list = list.concat(this.findValuesHelper(obj[children[i]], key, []));
          }
        }
      }
      return list;
    },
    onGoogleLoginSuccess(googleUser) {
      this.createAccount({
        payload: {
          provider: 'google',
          provider_id: false,
          provider_token: this.findValues(googleUser,'access_token')[0],
        },
        needReload: this.needReload,
      })
      this.show = true;
    },
    onGoogleLoginFailure(googleUser) {
      
    },
    socialLogin(provider, response) {},
  },
  computed:{
    createId(){
      let currentUrl = this.$route.name;
      if(currentUrl.indexOf('requests-post-preview') >= 0){
        return `postRequest-login`
      }else if (currentUrl.indexOf('properties-add-6') >= 0){
        return `addListing-login`
      }else if(currentUrl.indexOf('marketplace') >= 0){
        return `marketplace-login`
      }else if(currentUrl.indexOf('index') >= 0){
        return `homepage-login`
      }
    }
  }
  // watch: {
  //   dialog: {
  //     handler(value) {
  //       if (!value) {
  //         this.FB = null
  //       }
  //     },
  //     immediate: true,
  //   },
  // },
}
</script>

<style lang="scss" scoped>
.social-login-container {
  padding: 15px 0;
  .social-login-button {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    border-color: $grayLightColor;
    padding: 10px 15px;
    color: $mainTextColor;
    &:hover,
    &:focus,
    &:active {
      border-color: $grayLightColor !important;
      background-color: $grayBackgroundColor !important;
      color: $mainTextColor !important;
      box-shadow: none !important;
    }
  }
  .button-text {
    margin-left: 10px;
    margin-right: 10px;
  }
}
</style>
