<template>
  <b-modal
    id="auth-modal"
    centered
    hide-header
    hide-footer
    @hidden="onCloseModal"
    @show="resetResponse"
    @hide="resetResponse"
    v-model="authModel"
  >
    <b-overlay :show="authLoading">
      <transition-group name="slide">
        <div v-if="successResponse" key="successResponse">
          <b-alert show variant="success">
            {{ successResponse }}
          </b-alert>
        </div>
        <div v-if="serverErrors" key="serverErrors">
          <b-alert show variant="danger">
            <ul class="mb-0 p-0">
              <template v-if="typeof serverErrors === 'string'">
                {{ serverErrors }}
              </template>
              <template v-else>
                <li v-for="(errors, index) in serverErrors" :key="index">
                  <ul class="mb-0 p-0">
                    <li
                      v-for="(error, index) in errors"
                      :key="index"
                      style="text-align: center"
                    >
                      {{ error.errorMsg || error }}
                    </li>
                  </ul>
                </li>
              </template>
            </ul>
          </b-alert>
        </div>
      </transition-group>
      <component
        :is="components[componentIndex]"
        @success="success"
        :need-reload="needReload"
        @on-switch="onSwitchComponent"
        :dialog="authModel"
      />
      <!-- <LoginForm
        v-if="isLogin && !forgotPassword"
        :need-reload="needReload"
        @success="success"
        @openSignupForm="openSignupForm"
        @openForgotPasswordForm="openForgotPasswordForm"
      ></LoginForm>
      <SignupForm
        v-if="!isLogin && !forgotPassword"
        :need-reload="needReload"
        @success="success"
        @openLoginForm="openLoginForm"
      ></SignupForm> -->
      <!-- <ForgotPasswordForm
        @openLoginForm="openLoginForm"
        @openSignupForm="openSignupForm"
      ></ForgotPasswordForm> -->
    </b-overlay>
  </b-modal>
</template>

<script>
import LoginForm from './LoginForm'
import SignupForm from './SignupForm'
import ForgotPasswordForm from './ForgotPasswordForm'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('AuthModule')
export default {
  name: 'AuthModal',

  components: {
    LoginForm,
    SignupForm,
    ForgotPasswordForm,
  },
  data() {
    return {
      components: ['LoginForm', 'SignupForm', 'ForgotPasswordForm'],
      componentIndex: 0,
      isLogin: true,

      forgotPassword: false,
      authModel: false,
    }
  },
  computed: {
    ...mapState({
      visibility: (state) => state.authModal.visibility,
      callbackAction: (state) => state.authModal.callbackAction,
      serverErrors: (state) => state.serverErrors,
      authLoading: (state) => state.authLoading,
      successResponse: (state) => state.successResponse,
    }),
    needReload() {
      return !(this.callbackAction && this.callbackAction.name)
    },
  },
  watch: {
    visibility(newVal) {
      newVal
        ? this.$bvModal.show('auth-modal')
        : this.$bvModal.hide('auth-modal')
    },
    isLogin(newVal) {
      if (!newVal) return
      this.resetResponse()
    },
    forgotPassword(newVal) {
      if (!newVal) return
      this.resetResponse()
    },
    authModel: {
      handler(value) {
        if (!value) {
          this.componentIndex = 0

          this.forgotPassword = false
        }
      },
      immediate: true,
    },
  },
  methods: {
    ...mapActions(['hideAuthModal', 'resetResponse']),
    onSwitchComponent(componentName) {
      this.resetResponse()
      this.componentIndex = this.components.indexOf(componentName)
    },

    callback() {
      this.$store.dispatch(
        this.callbackAction.name,
        this.callbackAction.payload
      )
    },
    onCloseModal() {
      this.componentIndex = 0
      this.hideAuthModal()
    },
    success() {
      !this.needReload && this.callback()
      this.hideAuthModal()
      this.$emit('successGoogleLogin')
    },
  },
}
</script>

<style lang="scss" scoped>
::v-deep() .modal-body {
  padding: 25px;
}

ul,
li {
  list-style: none;
}
</style>
