<template>
  <form @submit.prevent="forgotPasswordHandler">
    <header class="form-header">
      <h5 class="form-title">forgot password</h5>
      <p class="form-description">Send Password Reset Link</p>
    </header>

    <AppInput
      :validation-name="$t('form.email')"
      :placeholder="$t('form.email')"
      type="email"
      autoComplete="email"
      required
      validation-rules="required|email"
      v-model="form.email"
    ></AppInput>
    <AppButton type="submit" block variant="primary" :disabled="authLoading">
      Send
    </AppButton>
    <footer class="d-flex align-items-center justify-content-between mt-3">
      <button
        type="button"
        @click="$emit('on-switch', 'SignupForm')"
        class="bg-transparent border-0 p-0 btn-link"
      >
        Register
      </button>
      <button
        type="button"
        @click="$emit('on-switch', 'LoginForm')"
        class="bg-transparent border-0 p-0 btn-link"
      >
        Login
      </button>
    </footer>
  </form>
</template>

<script>
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('AuthModule')
export default {
  data() {
    return {
      form: {
        email: '',
      },
    }
  },
  computed: {
    ...mapState(['authLoading', 'successResponse']),
  },
  methods: {
    ...mapActions(['forgotPassword']),
    forgotPasswordHandler() {
      this.forgotPassword({ email: this.form.email })
        .then((res) => {
          console.log(res)
        })
        .catch((err) => {})
    },
  },
}
</script>


<style lang="scss" scoped>
.form-header {
  margin-bottom: 25px;
}
.form-title {
  font-size: 18px;
}
.form-description {
  color: $secondaryTextColor;
  font-size: 12px;
  margin-top: 5px;
  margin-bottom: 0;
}
.form-message {
  display: flex;
  justify-content: center;
  button {
    border: none;
    margin: 0 10px;
  }
}
</style>
