import $axios from '@/helpers/axios-port'

export default {
  getUserProfile() {
    return $axios.$get('/user/userDetails')
  },
  compareOffers(payload) {
    return $axios.$get(`/user/compareOffers`, {
      params: {
        ids: payload,
      },
    })
  },
  getNotifcation() {
    return $axios.$get(`/user/allNotification`)
  },
  markAllAsRead() {
    return $axios.$post(`/user/readAllUnreadNotification`, {})
  },
  saveUserProfile(data) {
    let formObj = {
      ...data,
    }
    formObj.nationality = data.nationality?.replace(/ /g, '_')
    formObj[name] = data.full_name ? data.full_name : ''
    typeof formObj.avatar === 'string' ? delete formObj.avatar : null

    const formData = new FormData()
    for (let key in formObj) {
      formData.append(key, data[key])
    }

    return $axios.$post('/user/updateUser', formData)
  },
  getNationalites() {
    return $axios.$get('/user/getNationalities')
  },
}
