<template>
  <div :class="{
    'transparent-header': isHomePage && isTransparentHeader,
    'no-padding-top': isHomePage,
  }">
    <div class="upperHeader bg-primary text-white py-2 text-center px-2">RentUp Beta Version Now LIVE! SignUp to try our services</div>
    <AppHeader class="d-none d-md-block" />
    <!-- <DashboardHeader
      class="dashboard-header"
      :class="{
        'sidebar--visible': visible,
        'messaging-sidebar--visible': MessagingSidebarIsOpen,
        'move-header': messageSideBar,
      }"
      :isVisible="visible"
    /> -->
    <AuthModal />
    <div class="main-content-container" style="min-height: 100vh" :class="{
      'sidebar--visible': visible,
      'messaging-sidebar--visible': MessagingSidebarIsOpen,
    }">
      <Nuxt />
      <AppFooter />
      <LowerNav />
      <noscript v-if="this.$config.APP_ENV == 'production'"><iframe
          src="https://www.googletagmanager.com/ns.html?id=GTM-5Z3VS3H" height="0" width="0"
          style="display: none; visibility: hidden"></iframe></noscript>
    </div>
    <SendOfferModal />
  </div>
</template>

<script>
import AppHeader from '@/components/shared/AppHeader'
import DashboardHeader from '@/components/shared/DashboardHeader'
import AppFooter from '@/components/shared/AppFooter'
import AuthModal from '@/components/shared/AuthModal'
import SendOfferModal from '~/components/favorite-properties/SendOfferModal'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('UserModule')
import LowerNav from '@/components/onboarding/LowerNav.vue'


export default {
  middleware: 'require-auth',
  components: {
    AppHeader,
    DashboardHeader,
    AppFooter,
    AuthModal,
    SendOfferModal,
    LowerNav,
  },
  head() {
    return {
      ...this.$nuxtI18nHead({ addSeoAttributes: true }),
      htmlAttrs: {
        lang: this.$i18n.locale,
      },
      meta: [

      ],
      script: [
        this.$config.APP_ENV == 'production' ? {
          hid: 'google-manager',
          type: 'text/javascript',
          innerHTML:
            "(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src='https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);})(window,document,'script','dataLayer','GTM-5Z3VS3H');",
        } : {},
      ],
      __dangerouslyDisableSanitizersByTagID: {
        'google-manager': ['innerHTML'],
        'google-analytics': ['innerHTML'],
      },
    }
  },

  data() {
    return {
      scrollEventDebounce: null,
      isTransparentHeader: false,
      scrollEventActivated: false,
      visible: false,
      MessagingSidebarIsOpen: false,
      messageSideBar: false,
    }
  },
  computed: {
    ...mapState(['user']),
    isHomePage() {
      return this.$route?.name?.includes('index')
    },
    isRTL() {
      return this.$store.getters['CoreModule/isRTL']
    },
    activeChat() {
      return this.$store.state.MessagingModule.activeChat
    },
    userID() {
      return this.$store.state.UserModule.user
    },
  },
  methods: {
    ...mapActions(['getUserProfile']),
    handleScrollEvent() {
      clearTimeout(this.scrollEventDebounce)
      this.scrollEventDebounce = setTimeout(() => {
        if (window.scrollY > 300) {
          this.isTransparentHeader = false
        } else {
          this.isTransparentHeader = true
        }
      }, 150)
    },
    addScrollEvent() {
      this.isTransparentHeader = true

      window.addEventListener('scroll', this.handleScrollEvent)
    },
    removeScrollEvent() {
      window.removeEventListener('scroll', this.handleScrollEvent)
    },
    handleOnScroll(path) {
      if (typeof window === 'undefined') return
      if (this.isHomePage) {
        !this.scrollEventActivated && this.addScrollEvent()
      } else {
        this.removeScrollEvent()
      }
    },
    onWindowWidthChange() {
      window.addEventListener('resize', () => {
        const width = window.innerWidth
        if (width <= 1441) {
          this.visible = false
        } else {
          this.visible = true
        }
      })
    },
    handleNotification() {
      if (this.userID) {
        this.$echo
          .private(`App.Models.User.${this.userID.user_id}`)
          .notification((e) => {
            this.$store.dispatch('UserModule/getNotifcation')
          })
      }
    },
  },
  mounted() {

    this.handleNotification()
    this.onWindowWidthChange()
    this.handleOnScroll(this.$route.path)

    this.$root.$on('MessagingSidebarIsOpen', (val) => {
      this.messageSideBar = val
    })
    // this.$root.$on('MessagingSidebarIsOpen', (val) => {
    //   if (val) this.visible = false
    //   this.MessagingSidebarIsOpen = val
    // })
    this.getUserProfile()
  },
  watch: {
    $i18n: {
      handler(value) {
        if (process.client) {
          if (this.$i18n.locale === 'ar') {
            document.body.lang = 'ar'
            document.body.dir = 'rtl'
          } else {
            document.body.lang = 'en'
            document.body.dir = 'ltr'
          }
        }
      },
      immediate: true,
    },
    $route(to) {
      this.handleOnScroll(to.path)
    },
    activeChat: {
      handler(newValue) {
        if (!newValue) return
        if (window.innerWidth < 768) {
          this.visible = false
        }
      },
      immediate: true,
    },
    visible: {
      handler(newValue) {
        if (!newValue) return
        // if in mobile
        if (window.innerWidth < 768) {
          this.$root.$emit('DashboardSidebarIsOpen', newValue)
        }
      },
    },
  },
}
</script>

<style lang="scss" scoped>
.upperHeader {
  font-size: 13px !important;
  @media screen and (max-width: 400px){
    font-size: 11px !important;
  }
}
body[dir='ltr'], body[dir='ltr'] *{
  font-family: 'Inter', sans-serif !important;
}

body[dir='rtl'], body[dir='rtl'] *{
  font-family: 'Cairo', sans-serif !important;
}

.main-content-container {
  min-height: calc(100vh - 274px);
  /* fix footer issue with pages that not have enough content */
  // padding-top: 95px;

  /* header padding */
  .no-padding-top & {
    padding-top: 0;
  }

  &.sidebar--visible {
    max-width: calc(100vw);

    // @media screen and (max-width: 768px) {
    //   min-width: 100vw;
    //   transform: translateX(218px);
    // }

    // @media screen and (max-width: 1441px) {
    //   min-width: 100vw;
    //   transform: translateX(218px);
    // }
  }

  &.messaging-sidebar--visible {

    // max-width: calc(100vw - 218px);
    // margin-left: auto;
    @media screen and (min-width: 992px) {
      transform: translateX(-400px);
    }

    @media screen and (max-width: 768px) {
      min-width: 100vw;
    }
  }
}

.dashboard-header,
.dashboard-footer {
  &.sidebar--visible {
    width: calc(100vw);

    /* transform: translateX(218px); */
    @media screen and (max-width: 768px) {
      min-width: 100vw;
    }
  }

  &.messaging-sidebar--visible {

    // max-width: calc(100vw - 218px);
    @media screen and (min-width: 992px) {
      transform: translateX(-400px);
    }

    @media screen and (max-width: 768px) {
      min-width: 100vw;
    }
  }
}

:lang(ar) {
  .main-content-container {
    &.sidebar--visible {
      transform: translateX(-218px);
    }

    &.messaging-sidebar--visible {
      @media screen and (min-width: 992px) {
        transform: translateX(-400px);
      }
    }
  }

  .dashboard-header,
  .dashboard-footer {
    &.sidebar--visible {
      /* transform: translateX(-350px); */
    }

    &.messaging-sidebar--visible {
      @media screen and (min-width: 992px) {
        transform: translateX(400px);
      }
    }
  }
}

.move-header {
  transform: translateX(180px) !important;

  @media screen and (max-width: 992px) {
    transform: translateX(0px) !important;
  }
}
</style>
