<template>
  <div class="mt-3" v-if="meta.total > meta.per_page">
    <b-pagination
      :value="meta.current_page"
      :total-rows="meta.total"
      :per-page="meta.per_page"
      @change="handlePagination"
      pills
      size="md"
      align="center"></b-pagination>
  </div>
</template>

<script>
export default {
  name: 'Pagination',
  props: {
    meta: {
      type: Object,
      default: () => ({
        current_page: '',
        per_page: '',
        total: '',
      }),
    },
  },
  methods: {
    handlePagination(page) {
      this.$router.push({ query: { ...this.$route.query, page } });
    },
  },
};
</script>
