import Vue from 'vue'
import {
  ValidationProvider,
  ValidationObserver,
  extend,
  setInteractionMode,
} from 'vee-validate/dist/vee-validate.full.esm'
export default ({ app: { i18n } }) => {
  extend('phone', {
    validate: (value) => {
      const regex =
        /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im
      return regex.test(value)
    },
    message: i18n.t('errors.notValidPhoneNumber'),
  })

  extend('number', {
    validate: (value) => {
      return !isNaN(value)
    },
    message: i18n.t('errors.notValidNumber'),
  })

  extend('url', {
    validate(value) {
      // eslint-disable-next-line
      const regex =
        /^http(s)?:\/\/(www\.)?[a-z0-9]+([\-\.]{1}[a-z0-9]+)*\.[a-z]{2,5}(:[0-9]{1,5})?(\/.*)?$/
      return regex.test(value)
    },
    message: i18n.t('errors.notValidURL'),
  })

  extend('phoneOrEmail', {
    validate(value) {
      // eslint-disable-next-line
      const phoneRegex = /^(\+\d{1,3}[- ]?)?\d{10}$/,
        emailRegex =
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/

      return emailRegex.test(value) || phoneRegex.test(value)
    },
    message: i18n.t('errors.notValidEmailOrPhone'),
  })

  extend('phoneNumber', {
    validate(value) {
      const phoneRegex = /^(\+2)?01[0125][0-9]{8}$/
      return phoneRegex.test(value)
    },
    message: i18n.t('errors.notValidPhoneNumber'),
  })

  extend('listingPrices', {
    validate(value) {
      const pricesRegex = /^[1-9]/
      return pricesRegex.test(value)
    },
    message: i18n.t('errors.cannotStartWithZero'),
  })

  setInteractionMode('lazy')

  Vue.component('ValidationProvider', ValidationProvider)
  Vue.component('ValidationObserver', ValidationObserver)
}
