      <svg-icon name="golden-star" v-else />
    </div>
    <div class="rate-point" v-if="showReview">{{ rate }}</div>
    <div class="reviews-count" v-if="!hasBorder && showReview">
      ({{ reviewsCount }} {{ $t('common.reviews') }})
    </div>
  </div>
</template>
<script>
import StarRating from '@/components/shared/StarRating.vue'
export default {
  components: {
    StarRating,
  },
  props: {
    withStarRating: {
      type: Boolean,
      default: false,
    },
    showReview: {
      type: Boolean,
