export default {
  setFriends(state, data) {
    state.recentSender = data.recentSender ? data.friends[0] : null
    state.friends = data.friends
    if (!state.friendsMeta) return

    //  remove saved allFriends if filter property is activated
    if (data.filterProperty) {
      state.allFriends = []
      state.friends = []
    }

    if (data.recentSender) {
      // remove last friend in list except when it is loaded fully

      if (state.friendsMeta.total > 10) state.allFriends.pop()

      // add recent Sender to top except when he is the active chat

      state.allFriends =
        state.activeChat && state.activeChat && data.friends && data.friends.length && state.activeChat.friend_id == data.friends[0].id ? [...state.allFriends] : [
          ...data.recentSenderObj,
          ...state.allFriends.filter(
            (ele) => ele.id !== data.recentSenderObj[0].id
          ),
        ]
    } else {
      // fetch friends
      if (state.allFriends.length < state.friendsMeta.total) {
        state.allFriends = [...(state.allFriends ? state.allFriends : []), ...(data.friends ? data.friends : [])]
      }
    }
  },
  setFriendUnreadMessages(state, { id, session_id }) {
    state.allFriends.find((friend) => friend.id === id && friend.session.id === session_id).session.unreadCount = 0
  },
  setFriendsMeta(state, payload) {
    state.friendsMeta = payload
  },
  setMessages(state, data) {
    state.messages = data.messages
    if (
      state.messagesMeta &&
      state.allMessages.length < state.messagesMeta.total
    ) {
      state.allMessages = data.lastMessage ? [...data.messages, ...state.allMessages] : [...state.allMessages, ...data.messages]
    }
  },
  setMessagesMeta(state, messages) {
    state.messagesMeta = messages
  },
  activateChat(state, payload) {
    state.allMessages = []
    state.activeChat = payload
  },
  setLoader(state, {
    loader,
    value
  }) {
    state[loader] = value
  },
  setFriendsProperties(state, data) {
    state.friendsProperties = data
  },
  setSingleProperty(state, data) {
    state.singleProperty = data
  },
  setSingleOffer(state, data) {
    state.singleOffer = data
  }
}
