<template>
  <ValidationObserver v-slot="{ handleSubmit }">
    <form @submit.prevent="handleSubmit(submit)">
      <!-- sign up title -->
      <div class="form-header">
        <h5 class="form-title">{{ $t('header.signup') }}</h5>
        <p class="form-description">Sign up to start you rental experience</p>
      </div>
      <!-- full name input -->
      <AppInput
        :validation-name="$t('form.fullName')"
        :placeholder="$t('form.fullName')"
        type="text"
        autoComplete="name"
        required
        validation-rules="required"
        v-model="form.name"
      ></AppInput>
      <!-- email input -->
      <AppInput
        :validation-name="$t('form.email')"
        :placeholder="$t('form.email')"
        type="email"
        autoComplete="email"
        required
        validation-rules="required|email"
        v-model="form.email"
      ></AppInput>
      <!-- password input -->
      <AppInput
        :validation-name="$t('form.password')"
        :placeholder="$t('form.password')"
        type="password"
        autoComplete="new-password"
        required
        validation-rules="required|min:6"
        :hintMessage="$t('form.password_hint_message')"
        v-model="form.password"
      ></AppInput>
      <label class="mb-0 font-weight-bold">{{ dir === 'ltr' ? 'User Type' : ' ' }}</label>
      <b-dropdown :text="form.type" variant="light" class="my-2 mb-3" menu-class="w-100" block>
        <template #button-content>
          <div class="dropdown-header d-flex align-items-center justify-content-between w-100 px-1">
            <h3 class="mb-0">{{ form.type }}</h3>
            <img src="@/assets/images/icons/downCaret.svg" alt="Down Caret" width="20px" height="20px">
          </div>
        </template>
        <b-dropdown-item @click="form.type = option" v-for="option in options" :key="option">{{ option }}</b-dropdown-item>
      </b-dropdown>

      <!-- phone input -->
      <fieldset class="form-group -wrapper">
        <!-- libary of country code -->
        <vue-tel-input
          :validation-name="$t('form.mobileNumber')"
          :placeholder="$t('form.mobileNumber')"
          type="tel"
          autoComplete="tel"
          required
          validation-rules="required|phoneNumber"
          v-model="form.phone_number"
          mode="international"
          dropdownOptions.showSearchBox="true"
          defaultCountry="EG"
        ></vue-tel-input>
      </fieldset>

      <!-- sign up btn -->
      <AppButton :id="createId" type="submit" block variant="primary" :loading="authLoading">
        {{ $t('header.signup') }}
      </AppButton>
      <!-- seprator -->
      <Separator :text="$t('common.or')" />
      <!-- google login btn -->
      <SocialLogin
        @success="loginSuccess"
        :need-reload="needReload"
        :dialog="dialog"
      />
      <!-- login txt -->
      <p class="form-message">
        {{ $t('header.haveAccountMessage') }}
        <b-button
          variant="link"
          class="p-0"
          @click="$emit('on-switch', 'LoginForm')"
        >
          {{ $t('header.login') }}
        </b-button>
      </p>
    </form>
  </ValidationObserver>
</template>

<script>
import Separator from '@/components/shared/Separator'
import SocialLogin from '@/components/shared/SocialLogin'
import { createNamespacedHelpers } from 'vuex'
import { VueTelInput } from 'vue-tel-input'
import 'vue-tel-input/dist/vue-tel-input.css'

const { mapState, mapActions } = createNamespacedHelpers('AuthModule')
export default {
  components: {
    Separator,
    SocialLogin,
    VueTelInput,
  },
  props: {
    needReload: {
      required: true,
      type: Boolean,
    },
    dialog: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      form: {
        name: null,
        email: null,
        phone_number: null,
        password: null,
        type: 'Private Owner / Tenant',
        // userType
      },
      options: [],
    }
  },
  computed: {
    ...mapState(['authLoading']),
    createId(){
      let currentUrl = this.$route.name;
      if(currentUrl.indexOf('requests-post-preview') >= 0){
        return `postRequest-login`
      }else if (currentUrl.indexOf('properties-add-6') >= 0){
        return `addListing-login`
      }else if(currentUrl.indexOf('marketplace') >= 0){
        return `marketplace-login`
      }else if(currentUrl.indexOf('index') >= 0){
        return `homepage-login`
      }
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
  methods: {
    // submit action
    ...mapActions(['createAccount']),
    submit() {
      this.createAccount({
        payload: this.form,
        needReload: this.needReload,
      })
      // if success
        .then((success) => {
          if (success) {
            this.loginSuccess(true)
            this.$bvToast.toast(this.$t('form.signup_success'), {
              variant: 'success',
              toaster: 'b-toaster-top-center',
              solid: true,
              bodyClass: ['toast-body'],
              headerClass: ['toast-header'],
              noCloseButton: true,
            })
          }
        })
      // else catch error
        .catch((err) => {
          console.log(err.response, 'response')
        })
    },
    loginSuccess(status) {
      setTimeout(() => {
        if (status) this.$emit('success')
      }, 1000)
    },
  },
  mounted(){
    if(this.dir === 'ltr'){
      this.options = ['Private Owner / Tenant', 'Broker']
    }else{
      this.options = [' /  ', ' ']
    }
  }
}
</script>

<style lang="scss">

#auth-modal___BV_modal_body_ .btn.dropdown-toggle {
  border: 1px solid #000 !important;
}
#auth-modal___BV_modal_body_ .dropdown-toggle {
  padding-left: 10px !important;
  padding-right: 10px !important;
}

#auth-modal___BV_modal_body_ a.dropdown-item {
  padding-left: 10px !important;
  padding-right: 10px !important;
}

#auth-modal___BV_modal_body_ .form-header {
  margin-bottom: 25px;
}
#auth-modal___BV_modal_body_ .form-title {
  font-size: 18px;
}
#auth-modal___BV_modal_body_ .form-description {
  color: $secondaryTextColor;
  font-size: 12px;
  margin-top: 5px;
  margin-bottom: 0;
}

#auth-modal___BV_modal_body_ .form-message {
  display: flex;
  justify-content: center;
  button {
    border: none;
    margin: 0 10px;
  }
}
</style>
