<template>
  <div class="notification-container">
    <b-dropdown variant="text" class="custom-dropdown">
      <template #button-content>
        <img :class="['mx-1', !isTransparent && isHomePage ? 'd-none' : '']" width="20px" height="20px" :src="require('@/assets/images/sidebar/Notification-bell-black.svg')" alt="White Notification bell">
        <img :class="['mx-1', !isTransparent && isHomePage ? '' : 'd-none']" width="20px" height="20px" :src="require('@/assets/images/sidebar/Notification-bell-white.svg')" alt="Black Notification bell">
        <sup
          class="notification-tab"
          v-if="notifications.length && notifications.length > 0"
          >{{ notifications.length }}</sup
        >
      </template>
      <b-overlay opacity=".8" :show="isLoading">
        <section class="all-notifications">
          <h3 class="mb-0">{{ $t('common.notifications') }}</h3>
          <b-alert
            class="rounded-0 mb-0 text-center"
            variant="secondary"
            show
            v-if="!notifications || !notifications.length"
          >
            {{ $t('common.noNotificationsForNow') }}
          </b-alert>
          <a
            class="msg_link"
            v-for="(notification, i) in notifications"
            :key="`notification${i}`"
            :href="notification.route_link"
          >
            <article class="notification_wrapper">
              <div class="message">
                {{ notification.noti_msg | shorten }}
              </div>
            </article>
          </a>
        </section>
        <section class="mark-all-as-read">
          <button
            @click="markAllAsReadHandler"
            class="bg-secondary py-1 w-100 p-0 border-0"
            v-if="notifications && notifications.length"
          >
            {{ $t('common.markAllAsRead') }}
          </button>
        </section>
      </b-overlay>
    </b-dropdown>
  </div>
</template>

<script>
export default {
  name: 'notification',
  props: {
    iconColor: {
      type: String,
      default: '#FFFFFF',
    },
    isTransparent: {
      default: true
    }
  },
  mounted() {
    this.handleNotification()
  },
  computed: {
    notifications() {
      return this.$store.state.UserModule.allNotification
    },
    userID() {
      return this.$store.state.UserModule.user.user_id
    },
    token() {
      return this.$store.state.AuthModule.token
    },
    isLoading() {
      return this.$store.state.UserModule.notificationLoading
    },
    isHomePage() {
      return this.$route?.name?.includes('index');
    },
  },
  methods: {
    handleNotification() {
      if (this.userID) {
        this.$echo
          .private(`App.Models.User.${this.userID}`)
          .notification((e) => {
            this.$store.dispatch('UserModule/getNotifcation')
          })
      }
    },
    markAllAsReadHandler() {
      this.$store.dispatch('UserModule/markAllAsRead')
    },
  },
  filters: {
    shorten: function (value) {
      if (!value) return ''
      value = value.toString()
      return value.length > 100 ? value.slice(0, 100) + ' ...' : value
    },
  },
}
</script>

<style lang="scss" scoped>
h3 {
  font-size: 18px;
  padding: 1em;
  text-transform: capitalize;
  background: #f5f5f5;
  font-weight: bold;
}
.notification_wrapper {
  transition: all 0.3s ease-in-out;
  padding: 1em;
  border-bottom: 2px solid #eee;
  &:hover {
    background-color: #f5f5f5;
  }
}
.msg_link {
  color: inherit;
  text-decoration: none;
}

::v-deep() .custom-dropdown {
  .dropdown-menu {
    max-width: 80vw;
    width: 359px;
    transition: 0.2s;
    border: none;
    border-radius: 5px;
    overflow: hidden;
    right: 0 !important;
    .all-notifications {
      max-height: 80vh;
      overflow-y: scroll;
    }
    border-radius: 5px;
    box-shadow: 0 3px 6px 0 $grayTransparentColor;
    position: absolute;
    top: 35px;
    left: auto;
    background: #fff;
    z-index: 9999;
    padding: 0;
    margin: 0;
    list-style: none;
    &::-webkit-scrollbar {
      width: 10px;
    }
    &::-webkit-scrollbar-track {
      background: rgba(15, 15, 20, 0.1);
    }
    &::-webkit-scrollbar-thumb {
      background: rgba(15, 15, 20, 0.4);
      border-radius: 5px;
    }
    li {
      padding: 14px 0;
      border-bottom: 1px solid $grayTransparentColor;
    }
  }
}
.user-img {
  padding-top: 100%;
  position: relative;
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}
.message {
  font-size: 12px;
  color: $secondaryTextColor;
}

[dir='rtl'] {
  .custom-dropdown {
    ::v-deep() .dropdown-menu {
      right: auto !important;
      left: 0 !important;
    }
  }
}
@include media-breakpoint-down(sm) {
  ::v-deep() .custom-dropdown {
    .dropdown-menu {
      width: 75vw;
      max-width: 300px;
      li {
        padding: 10px 0;
      }
    }
  }
}
.notification-tab {
  width: 18px;
  height: 18px;
  background-color: rgba(255, 0, 0, 0.459);
  border-radius: 50%;
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  color: white;
  [dir='rtl'] & {
    right: unset;
    left: 0;
  }
}
</style>

<style lang="scss">
.dropdown-toggle {
  display: flex;
  padding-left: 0;
}
[dir='rtl'] .dropdown-toggle {
  padding-right: 0;
  padding-left: 12px;
}
.dropdown-toggle::after {
  display: none;
}
</style>
