import $axios from '@/helpers/axios-port'
export default {
  getFriends(param) {
    return $axios
      .$get(`/chat/getChatFriends${param ? param : '?page=1&per_page=10'}`)
      .then((res) => res.Data)
  },
  getMessages(session_id, property_id, params = null) {
    if (!session_id || !property_id) return

    return $axios
      .$get(
        `/chat/session/${session_id}/${property_id}/chats${params && params != '' ? params : '?page=1&per_page=10'
        }`
      )
      .then((res) => res)
  },
  sendMessage(messageObj, session_id) {
    return $axios
      .$post(`/chat/send/${session_id}`, messageObj)
      .catch((err) => { })
  },
  readRecentMessage(session_id, property_id) {
    if (!session_id || !property_id) return
    return $axios
      .$post(
        `/chat/session/${session_id}/${property_id}/readRecentMessage`,
        null
      )
      .then((res) => res)
  },
  getFriendsProperties() {
    return $axios.$get(`/chat/getAllFriends`).then((res) => res)
  },
  getSingleFriends(payload) {
    return $axios.$get(`/chat/getSingleFriend?friend_id=${payload.id}`, {}).then((res) => res)
  },
  getSingleProperty(property_slug) {
    if (!property_slug) return Promise.reject('No property slug provided')
    return $axios.$get(`/getSingleProperty/${property_slug}`, {}).then((res) => res)
  },
  getSingleOffer(id) {
    if (!id) return Promise.reject('No property id provided')
    return $axios.$get(`/user/getSingleOffer?id=${id}`, {})
  }
}
