import $axios from '@/helpers/axios-port'
const mapPropertiesIdsToQueryParameters = (propertyIds) => {
  return propertyIds.map((propertyId) => `properties[]=${propertyId}`).join('&')
}
export default {
  getSentOffersProperties(params) {
    return $axios.$get(
      `/user/getSentOffersProperties?page=${params.page}&per_page=${
        params.perPage
      }${params.status ? `&status=${params.status}` : ''}${
        params.selectedProperties.length == 0
          ? ''
          : `&${mapPropertiesIdsToQueryParameters(params.selectedProperties)}`
      }`
    )
  },
  getSentOffers(params) {
    return $axios.$get(
      `/user/getUserPropertiesWithOffers?offer_id=${params.offerId}&date=${params.date}`
    )
  },
  getSentOffersForFilter() {
    return $axios.$get('/user/getAllSentOffersPropertiesOnly')
  },
  getFavourites(payload) {
    return $axios
      .$get(
        `/user/getFavourites?per_page=${payload.perPage}&page=${payload.page}`
      )
      .then((res) => {
        return res.Data
      })
  },
  getSinglePropertyDetails(propertySlug, term) {
    return $axios
      .$get(`/getSingleProperty/${propertySlug}?term=${term}`)
      .then((res) => {
        return res
      })
  },
  getSimilarProperties(propertyId, propertySlug) {
    return $axios.$get(
      `/getSimilarProperties?property_id=${propertyId}&slug=${propertySlug}`
    )
  },
  getPropertyReviews(propertyId, perPage) {
    return $axios.$get(`/getPropertyReviews`, {
      params: {
        property_id: propertyId,
        ...(perPage ? { per_page: perPage } : {}),
      },
    })
  },
  updatePropertyFavourite(payload) {
    return $axios.$post('/user/updateFavourite', payload)
  },
  sendOffer(payload) {
    return $axios.$post('/user/sendOffer', payload)
  },
  getSingleOffer(offerId) {
    return $axios.$get(`/user/getSingleOffer?id=${offerId}`)
  },
  acceptRejectOffer(payload) {
    return $axios.$post(`/user/acceptRejectOffer`, payload)
  },
  getRejectionReasons() {
    return $axios.$get('/user/rejectionReasons')
  },
  getTenantReviewNotes(payload) {
    return $axios.$get(`/user/tenantReviewNotes`, {
      params: {
        tenant_id: payload.tenant_id,
        user_id: payload.tenant_id,
        ...(payload.per_page ? { per_page: payload.per_page } : {}),
      },
    })
  },
  getPropMasterData() {
    return $axios.$get('/getPropMasterData').then((res) => res.Data)
  },
  getHotelType() {
    return $axios.$get('/getPropertyHotelTypes').then((res) => res.Data)
  },
  getChildRoot(id) {
    return $axios
      .$get(`/getChildrenRoutes/${id}`)
      .then((res) => res.Data.rootLocations)
  },
  getProperty(content) {
    const searchText = ['/', '?'].indexOf(content.search_text[0]) >= 0 ? content.search_text : `/${content.search_text}`
    return $axios
      .$get(`/searchProperty${content ? searchText : ''}`, {
        params: {
          // term: content.term || localStorage.getItem('searchPropertyTerm'),
          per_page: content.perPage,
          page: content.page,
          bath: content.bath,
          bed: content.bed,
          sort: content.sort,
          pf: content.pf,
          pt: content.pt,
          amenity: content.amenity,
          inCompound: content.inCompound,
          price: content.price && {
            min: content.price[0],
            max: content.price[1],
          },
        },
      })
      .then((res) => {
        return res
      })
  },
  getSearchGraph(content) {
    return $axios
      .$get(`/searchGraph${content ? content.search_text : ''}`, {
        params: {
          location_id: content.id, // localStorage.getItem('searchLocationId')
          term: content.term,
          per_page: content.perPage,
          per_page: content.perPage,
          page: content.page,
          bath: content.bath,
          bed: content.bed,
          sort: content.sort,
          pf: content.pf,
          pt: content.pt,
          amenity: content.amenity,
          inCompound: content.inCompound,
          price: content.price && {
            min: content.price[0],
            max: content.price[1],
          },
        },
      })
      .then((res) => {
        return res
      })
  },
  mapPropertiesIdsToQueryParameters(propertyIds) {
    return propertyIds
      .map((propertyId) => `properties[]=${propertyId}`)
      .join('&')
  },
  getMyProperties(payload) {
    return $axios.$get(
      `/user/getUserPropertiesWithOffers?allProperties=true&per_page=${
        payload.perPage
      }&page=${payload.page}${
        payload.status ? `&status=${payload.status}` : ''
      }${payload.offers ? '&offers=' + payload.offers : ''}${
        payload.selectedProperties.length == 0
          ? ''
          : `&${mapPropertiesIdsToQueryParameters(payload.selectedProperties)}`
      }`
    )
  },
  getMyPropertiesPage(payload) {
    return $axios.$get(
      `/user/getMyProperties?per_page=${payload.perPage}&page=${payload.page}${
        payload.status ? `&status=${payload.status}` : ''
      }${
        payload.selectedProperties.length == 0
          ? ''
          : `&${mapPropertiesIdsToQueryParameters(payload.selectedProperties)}`
      }`
    )
  },
  getMyPropertiesOffers(payload) {
    return $axios.$get(
      `/user/getMyPropertiesOffers?per_page=${payload.perPage}&page=${
        payload.page
      }${payload.status ? `&status=${payload.status}` : ''}${
        payload.selectedProperties.length == 0
          ? ''
          : `&${mapPropertiesIdsToQueryParameters(payload.selectedProperties)}`
      }`
    )
  },
  getPropertyOffers(payload) {
    return $axios.$get(
      `/user/getUserPropertiesWithOffers?property_id=${
        payload.propertyId
      }&per_page=${payload.perPage}&page=${payload.page}${
        payload.priceSort ? `&price=${payload.priceSort}` : ''
      }`
    )
  },
  getAllMyProperties() {
    return $axios.$get('/user/getAllUserProperties')
  },
  compareProperty(payload) {
    let ids = JSON.stringify(payload)
    return $axios.$get(`/compareProperty?data=${ids}`).then((res) => res.Data)
  },
  addProperty(payload) {
    return $axios.$post('/user/properties', payload)
  },
  getSimilarLocations(location_id) {
    return $axios.$get(`/similarLocations?location_id=${location_id}`)
  },
  updatePropertyOpen(payload) {
    return $axios.$post('/user/updatePropertyStatus', payload)
  },
  updateProperty(payload) {
    return $axios.$post('/user/updateProperty', payload)
  },
  getOldPropertyData(id) {
    return $axios.$get(`/user/editProperty/${id}`)
  },
  getLandLordReviews() {
    return $axios.$get(`/user/getLandLordReviews`).then((res) => res.Data)
  },
  storeOrUpdateLandLordReviews(payload) {
    return $axios.$post('/user/storeOrUpdateLandLordReviews', payload)
  },
  storeOrUpdateTenantReviews(payload) {
    return $axios.$post('/user/storeOrUpdateTenantReviews', payload)
  },
  getTenantReviews(tenantId) {
    return $axios.$get(`/getTenantReviews`, {
      params: {
        tenant_id: tenantId,
      },
    })
  },
  postRequest(payload) {
    return $axios.$post('/requests', payload)
  },
}
