<template>
  <b-modal
    ref="success-modal"
    id="success-modal"
    hide-footer
    @hidden="onCloseModal"
  >
    <template #modal-header>
      <h3 class="modal-title"></h3>

      <b-button size="sm" @click="onCloseModal" class="close-button">
        <svg-icon name="close-black" />
      </b-button>
    </template>
    <div class="success-icon">
      <svg-icon name="success-icon" />
    </div>
    <div>
      <h3 class="m-0 pt-2">{{ title }}</h3>
    </div>
    <div>
      <p class="m-0 pt-1 pb-2 text-center" v-html="description"></p>
    </div>
  </b-modal>
</template>
<script>
export default {
  name: 'SuccessModal',
  props: {
    title: {
      type: String,
    },
    description: {
      type: String,
    },
  },
  methods: {
    onCloseModal() {
      this.$bvModal.hide('success-modal');
    },
  },
  computed: {
  },
  watch: {
  },
}
</script>
