export const compressImage = ({
  file,
  options = {
    maxWidth: 2000,
    maxHeight: 2000,
  },
  callback,
}) => {
  const MIME_TYPE = 'image/jpeg';
  const QUALITY = 0.8;

  const blobURL = URL.createObjectURL(file);
  const image = new Image();

  image.onerror = function () {
    URL.revokeObjectURL(this.src);
    // Handle the failure properly
  };
  image.onload = function () {
    URL.revokeObjectURL(this.src);
    const [newWidth, newHeight] = calculateSize(
      image,
      options.maxWidth,
      options.maxHeight
    );
    const canvas = document.createElement('canvas');
    canvas.width = newWidth;
    canvas.height = newHeight;
    const ctx = canvas.getContext('2d');
    ctx.drawImage(image, 0, 0, newWidth, newHeight);
    canvas.toBlob(
      (blob) => {
        callback(
          new File([blob], file.name, {
            type: MIME_TYPE,
          })
        );
      },
      MIME_TYPE,
      QUALITY
    );
  };
  image.src = blobURL;

  // Utility functions for demo purpose
};

const calculateSize = (_image, maxWidth, maxHeight) => {
  let width = _image.width;
  let height = _image.height;

  // calculate the width and height, constraining the proportions
  if (width > height) {
    if (width > maxWidth) {
      height = Math.round((height * maxWidth) / width);
      width = maxWidth;
    }
  } else {
    if (height > maxHeight) {
      width = Math.round((width * maxHeight) / height);
      height = maxHeight;
    }
  }
  return [width, height];
};

const replaceCharacterByIndex = (string, index, replacement) => {
  if (string && typeof string === 'string' && index)
    return string.substring(0, index) + replacement + string.substring(index);
  else return '';
};

export const generateServerImageUrl = ({
  url,
}) => {
  return url;
};

export const dataURItoBlob = (dataURI) => {
  // convert base64/URLEncoded data component to raw binary data held in a string
  var byteString;
  if (dataURI.split(',')[0].indexOf('base64') >= 0)
    byteString = atob(dataURI.split(',')[1]);
  else byteString = unescape(dataURI.split(',')[1]);

  // separate out the mime component
  var mimeString = dataURI.split(',')[0].split(':')[1].split(';')[0];

  // write the bytes of the string to a typed array
  var ia = new Uint8Array(byteString.length);
  for (var i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }

  return new Blob([ia], { type: mimeString });
};
