import API from './api'
// import state from './state'

export default {
  getSearchRelatedInfo({ commit }, payload) {
    commit('setLoader', { loader: 'searchRelated', value: true })
    return API.getSearchRelatedInfo(payload)
      .then((res) => {
        commit('setSearchRelated', res.Data.related)
      })
      .then(() => {
        commit('setLoader', { loader: 'searchRelated', value: false })
      })
  },
  // getSearchLandScape({ commit, rootState }, payload) {
  //   commit('setLoader', { loader: 'searchLandscape', value: true })
  //   return API.getsearchLandScape(
  //     rootState.PropertyModule.filteredProperty.params
  //   )
  //     .then((res) => {
  //       commit('setSearchLandscape', res.Data)
  //     })
  //     .then(() => {
  //       commit('setLoader', { loader: 'searchLandscape', value: false })
  //     })
  // },
  getSearchGraph({commit, state}, payload) {
    payload = {...payload, id: state.searchLocationId}
    
    commit('setLoader', { loader: 'searchGraph', value: true })
    return API.getSearchGraph(payload)
      .then((res) => {
        commit('setSearchGraph', res.Data.graph_data)
      })
      .then(() => {
        commit('setLoader', { loader: 'searchGraph', value: false })
      })
  },
  setSearchLocationId({commit}, newVal){
    commit('setSearchLocationId', newVal)
  },
  setSearchPropertyTerm({commit}, newVal){
    commit('setSearchPropertyTerm', newVal)
  },
}
