<!-- header compnent to Mobile and Desktop -->
<template>
  <header ref="header" :class="`app-header ${isHomePage ? 'home' : ''}`">
    <div class="upperHeader bg-primary text-white py-2 mb-2 text-center px-2">RentUp Beta Version Now LIVE! SignUp to try our services</div>
    <b-container class="px-1">
      <!-- mobile nav bar -->
      <div class="mobile-nav">
        <div class="nav flex-nowrap">
          <!-- sidebar menu btn -->
          <div @click="showSideBar = !showSideBar" class="burger-menu mx-1">
            <span></span><span></span><span></span>
          </div>
          <!-- list property btn -->
          <a class="list-btn px-3" @click="handleListProperty">
            {{ $t('header.listMyProp') }}
          </a>
          <HeaderNav :visible="visible" />
        </div>
      </div>
      <!-- Desktop nav bar -->
      <div class="desktop-nav">
        <div class="logo-usermenu-container">
          <nuxt-link :to="localePath('/')" class="logo">
            <!-- rentup logo header -->
            <img
              class="d-block"
              :src="require(`@/assets/images/logo/rent-up.png`)"
              width="130px"
              height="48px"
              alt="Rentup logo" />
          </nuxt-link>
          <!-- Remove v-if="user" From Using The Component By Salem REN-202 -->
          <UserMenu />
        </div>
        <HeaderNav :visible="visible" />
      </div>
    </b-container>
    <SideBar :showSideBar.sync="showSideBar" />
  </header>
</template>

<script>
// import header nav from folder app header
import HeaderNav from './HeaderNav';
// import userMenu nav from folder app header
import UserMenu from './UserMenu';
import { createNamespacedHelpers } from 'vuex';

import Notification from '@/components/dashboard/Notification';

const { mapState } = createNamespacedHelpers('UserModule');
const { mapGetters } = createNamespacedHelpers('CoreModule');

export default {
  components: {
    Notification,
    HeaderNav,
    UserMenu,
    SideBar: () => import('./SideBar.vue'),
  },
  directives: {
    scroll: {
      bind(el) {
        if (window.innerWidth >= 768) {
          el.style.opacity = '1';
          el.style.visibility = 'visible';
          return;
        }
        document.addEventListener('scroll', (e) => {
          if (window.innerWidth <= 767) {
            if (window.scrollY > 50) {
              el.style.opacity = '1';
              el.style.visibility = 'visible';
            } else {
              el.style.opacity = '0';
              el.style.visibility = 'hidden';
            }
          }
        });
      },
    },
  },
  data() {
    return {
      visible: true,
      showSideBar: false,
    };
  },
  computed: {
    ...mapState(['user']),
    ...mapGetters(['isRTL']),
    availableLocale() {
      return this.$i18n.locales.find((i) => i.code !== this.$i18n.locale);
    },
    currentRoute() {
      return this.$route.path;
    },
    isHomePage() {
      return (
        this.$route.path === '/' ||
        this.$route.path === '/ar' ||
        this.$route.path === '/ar/'
      );
    },
  },
  watch: {
    currentRoute(newVal, oldval) {
      if (newVal != oldval) {
        this.visible = false;
        this.$refs.header.classList.remove('active-header');
      }
    },
  },
  methods: {
    handleListProperty() {
      this.$router.push(this.localePath('/properties/add/1'));
      this.gtag_report_conversion();
    },
    gtag_report_conversion(url) {
      var callback = function () {
        if (typeof url != 'undefined') {
          window.location = url;
        }
      };
      gtag('event', 'conversion', {
        send_to: 'AW-10826966991/tDkaCKHfj4wDEM_P2aoo',
        event_callback: callback,
      });
      return false;
    },
    onScroll() {
      this.windowTop = window.top.scrollY;
      if (this.windowTop > 100)
        this.$refs.header && this.$refs.header.classList.add('active-header');
      else
        this.$refs.header &&
          this.$refs.header.classList.remove('active-header');
    },
  },
  mounted() {
    if (process.browser) {
      window.addEventListener('scroll', this.onScroll);
    }
  },
};
</script>

<style lang="scss" scoped>
.app-header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  padding: 0 0 12px;
  background-color: #fff;
  transition: all 0.3s ease-in;
  .upperHeader {
    @media screen and (max-width: 400px) {
      font-size: 11px;
      font-weight: bold;
    }
  }
  ::v-deep() button,
  ::v-deep() a {
    color: #111;
  }

  &.home {
    ::v-deep() button,
    ::v-deep() a {
      color: #fff;
    }
  }

  ::v-deep() use {
    fill: #111111;
  }

  ::v-deep() .list-btn {
    border-color: #111111 !important;
  }

  &.active-header {
    box-shadow: 0 3px 6px 0 RGB(0 0 0 / 10%);
    background-color: #fff;
  }

  &.home {
    background-color: transparent;
    color: #fff;

    ::v-deep() button,
    ::v-deep() a {
      // color: #fff;
    }

    ::v-deep() use {
      fill: #111;
    }
    ::v-deep() button svg {
      fill: #111;
    }

    ::v-deep() .side-bar use {
      fill: #111;
    }

    ::v-deep() .list-btn {
      border-color: #fff !important;
    }

    &.active-header {
      box-shadow: 0 3px 6px 0 RGB(0 0 0 / 10%);
      background-color: #fff;

      ::v-deep() button,
      ::v-deep() a {
        color: #111;
      }

      ::v-deep() use {
        fill: #111111;
      }

      ::v-deep() .list-btn {
        border-color: #111111 !important;
      }
    }
  }

  .container {
    .mobile-nav {
      display: none;
      .nav {
        display: flex;
        justify-content: space-between;

        .burger-menu {
          background-color: #fff;
          width: 48px;
          height: 48px;
          clip-path: circle();
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;

          span {
            display: block;
            height: 2px;
            width: 20px;
            background-color: #111;
            margin-bottom: 5px;
            border-radius: 20px;

            &:last-child {
              margin: 0;
            }
          }
        }

        .list-btn {
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 6px 28px;
          border: 2px solid #fff;
          border-radius: 30px;
          font-weight: bold;
        }
      }
    }

    .desktop-nav {
      display: flex;
      justify-content: space-between;
      align-items: center;

      .logo-usermenu-container {
        display: flex;
        align-items: center;
        flex-grow: 1;
        img{
          margin-left:20px !important;
          margin-right:20px !important;
        }
        a {
          flex-grow: 1;
        }

        .user-menu {
          flex-grow: 8;
        }
      }
    }
  }
}

@media screen and (max-width: 767px) {
  .app-header {
    .container {
      // .mobile-nav {
      //   display: block;
      // }

      .desktop-nav {
        display: none;
      }
    }
  }
}
</style>
