export default {
  setSearchRelated(state, payload) {
    state.searchRelated = payload
  },
  // setSearchLandscape(state, payload) {
  //   state.searchLandscape = payload
  // },
  setSearchGraph(state, payload) {
    state.searchGraph = payload;
  },
  setLoader(state, { loader, value }) {
    state.loading[loader] = !!value
  },
  setSearchLocationId(state, newVal){
    state.searchLocationId = newVal;
  },
  setSearchPropertyTerm(state, newVal){
    state.searchPropertyTerm = newVal;
  },
}
