<template>
  <ul
    :class="['reset-list-style app-header-nav', { 'list-color': visible }]"
    class="header-nav">
    <!-- changing lang with arabic or english btn -->
    <li class="d-mobile-none mx-2">
      <b-button
        variant="text"
        class="lang-switch p-0"
        :href="switchLocalePath(availableLocale.code)"
        :disabled="isDisbaled">
        <img :class="['mx-1', !isTransparentHeader && isHomePage ? 'd-none' : '']" width="15px" height="15px" :src="require('@/assets/images/sidebar/World-black.svg')" alt="Black World">
        <img :class="['mx-1', !isTransparentHeader && isHomePage ? '' : 'd-none']" width="15px" height="15px" :src="require('@/assets/images/sidebar/World-white.svg')" alt="White World">
        {{ $t(`header.lang.${availableLocale.code}`) }}
      </b-button>
    </li>
    <!-- if user loged in message icon  will appear  -->
    <li v-if="user" class="mx-2" v-b-tooltip.hover.top="$t('common.messages')">
      <b-button
        variant="text"
        :to="localePath('/dashboard/messaging')"
        class="p-0">
        <img :class="['mx-1', !isTransparentHeader && isHomePage ? 'd-none' : '']" width="22px" height="22px" :src="require('@/assets/images/sidebar/Message-black.svg')" alt="Black Message">
        <img :class="['mx-1', !isTransparentHeader && isHomePage ? '' : 'd-none']" width="22px" height="22px" :src="require('@/assets/images/sidebar/Message-white.svg')" alt="White Message">
      </b-button>
    </li>
    <!-- if user loged in  notification icon will appear  -->
    <li v-if="user" v-b-tooltip.hover.top="$t('common.notification')">
      <Notification :isTransparent="isTransparentHeader && isHomePage" />
    </li>
    <!-- username if user loged in and if not login or registration btn will appear -->
    <li>
      <ProfileMenu :isTransparent="isTransparentHeader && isHomePage" v-if="user" @logout="handleLogout" class="d-mobile-none" />
      <!-- if user isn't loged in -->
      <div v-else>
        <b-button variant="text" class="p-0" @click="showAuthModal">
          <img :class="['mx-1', !isTransparentHeader && isHomePage ? 'd-none' : '']" width="15px" height="15px" :src="require('@/assets/images/sidebar/User-black.svg')" alt="White User">
          <img :class="['mx-1', !isTransparentHeader && isHomePage ? '' : 'd-none']" width="15px" height="15px" :src="require('@/assets/images/sidebar/User-white.svg')" alt="Black User">
          <span class="d-mobile-none">{{ $t('header.login') }}</span>
        </b-button>
      </div>
    </li>
  </ul>
</template>

<script>
// import notfication from commponent
import Notification from '@/components/dashboard/Notification';
// import profile menu from folder app header
import ProfileMenu from './ProfileMenu';
import { createNamespacedHelpers } from 'vuex';
const { mapActions: mapAuthActions } = createNamespacedHelpers('AuthModule');
const { mapState } = createNamespacedHelpers('UserModule');

const { mapGetters: mapCoreGetters } = createNamespacedHelpers('CoreModule');
export default {
  data() {
    return {
      isDisbaled: true,
      isTransparentHeader: false,
      displayMore: false,
    };
  },
  components: {
    ProfileMenu,
    Notification,
  },
  props: ['visible'],
  computed: {
    ...mapState(['user']),
    ...mapCoreGetters(['isRTL']),
    availableLocale() {
      return this.$i18n.locales.find((i) => i.code !== this.$i18n.locale);
    },
    isHomePage() {
      return this.$route?.name?.includes('index');
    },
    fillLangIcon() {
      return '#FFFFFF';
    },
    variantListButton() {
      if (this.isHomePage) {
        if (this.isTransparentHeader) {
          return 'primary';
        }
        if (this.visible) return 'primary';
        return '';
      } else {
        return 'primary';
      }
    },
  },
  methods: {
    ...mapAuthActions(['showAuthModal', 'logout']),
    handleLogout() {
      this.$bvToast.toast(this.$t('form.logout_message'), {
        variant: 'success',
        toaster: 'b-toaster-top-center',
        solid: true,
        bodyClass: ['toast-body'],
        headerClass: ['toast-header'],
        noCloseButton: true,
      });
      setTimeout(() => {
        this.logout();
      }, 1000);
    },
    onScroll() {
      this.windowTop = window.top.scrollY;
      if (this.windowTop > 100) {
        setTimeout(() => {
          this.isTransparentHeader = true;
        }, 50);
      } else {
        setTimeout(() => {
          this.isTransparentHeader = false;
        }, 50);
      }
    },
    handleListProperty() {
      this.$router.push(this.localePath('/properties/add/1'));
      this.gtag_report_conversion();
    },
    gtag_report_conversion(url) {
      var callback = function () {
        if (typeof url != 'undefined') {
          window.location = url;
        }
      };
      gtag('event', 'conversion', {
        send_to: 'AW-10826966991/tDkaCKHfj4wDEM_P2aoo',
        event_callback: callback,
      });
      return false;
    },
  },
  beforeDestroy() {
    if (process.browser) {
      window.removeEventListener('scroll', this.onScroll);
    }
  },
  mounted() {
    this.isDisbaled = false;
    if (process.browser) {
      window.addEventListener('scroll', this.onScroll);
    }
    window.addEventListener('resize', this.onResize);
  },
};
</script>

<style lang="scss" scoped>
.home .add-list-btn {
  @include media-breakpoint-down(lg) {
    display: none;
  }
}
.home .app-header-nav .bt-light {
  background-color: transparent;
  border: white solid 1px;
  border-radius: 16px;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 5px;
}
.header-nav {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0 5px !important;
}
.user-container {
  display: flex;
  align-items: center;
  gap: 10px;
  .user-avatar {
    width: 48px;
    height: 48px;
    border-radius: 50%;
  }
}
.list-color {
  color: $mainTextColor;
}
.app-header-nav {
  li {
    text-align: center;
    &:not(.has-button) {
      a {
        font-weight: normal;
        @include media-breakpoint-down(lg) {
          font-size: 12px;
        }
      }
    }

    @include media-breakpoint-up(lg) {
      display: inline-block;
      margin-left: 10px;
      [dir='rtl'] & {
        margin-left: 0;
        margin-right: 10px;
      }
    }
  }
  @include media-breakpoint-down(md) {
    .border-md-0 {
      border: none;
    }
    li {
      text-align: center;
      &.has-button,
      a.lang-switch,
      a:not(.btn) {
        padding: 15px 0;
        display: block;
        text-align: center;
      }
      &:last-child {
        a {
          padding-bottom: 0;
        }
      }
    }
  }
  .list-property-btn {
    background-color: transparent;
    color: #002bb9;
    border: 1px solid #002bb9 !important;
    font-weight: normal;
  }
  .list-property-btn:hover {
    background-color: white;
    color: black;
  }
  .more-button {
    color: #333333;
  }
  .lang-switch {
    display: flex;
    align-items: center;
    gap: 6px;
  }
}
.app-header .bt-light {
  border-color: #0078ff;
  font-weight: 700;
  color: #0078ff;
  padding: 7px 17px;
  border-radius: 15px;
  box-shadow: none !important;
  outline: none !important;
}

@media screen and (max-width: 767px) {
  .d-mobile-none {
    display: none;
  }
}
</style>
<style lang="scss">
.account-button {
  .dropdown-toggle {
    border: none;
    padding: 0;

    &:after {
      display: none;
    }
  }
}
</style>
