export default {
  setMyPropertiesData(state, payload) {
    state.myProperties.data = payload.data;
    state.myProperties.total = payload.total;
  },
  setMyPropertiesStatus(state, payload) {
    state.myProperties.status = payload;
  },
  updateMyPropertiesLoading(state, loading) {
    state.myProperties.loading = loading;
  },
  updateMyPropertiesParams(state, params) {
    const updatedPageParam = params.page
      ? {}
      : {
          page: 1,
        };
    state.myProperties.params = {
      ...state.myProperties.params,
      ...params,
      ...updatedPageParam,
    };
  },
  setMyPropertiesPageData(state, payload) {
    state.myPropertiesPage.data = payload.data;
    state.myPropertiesPage.total = payload.total;
  },
  setMyPropertiesPageStatus(state, payload) {
    state.myPropertiesPage.status = payload;
  },
  updateMyPropertiesPageLoading(state, loading) {
    state.myPropertiesPage.loading = loading;
  },
  updateMyPropertiesPageParams(state, params) {
    const updatedPageParam = params.page
      ? {}
      : {
          page: 1,
        };
    state.myPropertiesPage.params = {
      ...state.myPropertiesPage.params,
      ...params,
      ...updatedPageParam,
    };
  },
  setSentOffersPropertiesData(state, payload) {
    state.sentOffersProperties.data = payload.data;
    state.sentOffersProperties.total = payload.total;
  },
  updateSentOffersPropertiesLoading(state, loading) {
    state.sentOffersProperties.loading = loading;
  },
  updateSentOffersPropertiesParams(state, params) {
    let updatedPageParam = params.page
      ? {}
      : {
          page: 1,
        };
    state.sentOffersProperties.params = {
      ...state.sentOffersProperties.params,
      ...params,
      ...updatedPageParam,
    };
  },
  setFavourites(state, payload) {
    state.myFavourites.data = payload.data;
  },
  setSimilarProperties(state, payload) {
    state.similarProperties.data = payload.data;
  },
  updateSimilarPropertiesLoading(state, loading) {
    state.similarProperties.loading = loading;
  },
  updateMyFavouritesLoading(state, loading) {
    state.myFavourites.loading = loading;
  },
  setPropertyDetails(state, propertyDetails) {
    state.propertyDetails = propertyDetails;
  },
  setPropertyReviews(state, propertyReviews) {
    state.propertyReviews = propertyReviews;
  },
  setPropertyQuestions(state, propertyQuestions) {
    state.reviewQuestions.propertyQuestions = propertyQuestions;
  },
  setNeighbourQuestions(state, neighbourQuestions) {
    state.reviewQuestions.neighbourQuestions = neighbourQuestions;
  },
  updateIsFavourite(state, payload) {
    switch (payload.type) {
      case 'single-property':
        state.propertyDetails.isFav = !state.propertyDetails.isFav;
        break;
      case 'similar-properties':
        let similarProperties =
          state.similarProperties.data.nearbyproperty.find(
            (item) => item.property_id === payload.prop_id
          );
        if (similarProperties != undefined) {
          similarProperties.isFav = !similarProperties.isFav;
        }
        break;
      case 'search-property':
        let filteredProperty = state.filteredProperty.data.properties.find(
          (item) => item.property_id === payload.prop_id
        );
        if (filteredProperty != undefined) {
          filteredProperty.isFav = !filteredProperty.isFav;
        }
    }
  },
  setPropertyLoading(state, loading) {
    state.property.isLoading = loading;
  },
  updateSendOfferModalVisibility(state, payload) {
    state.sendOfferModal.visibility = payload.visibility;
    state.sendOfferModal.data = payload.data;
  },
  updateConfirmationModalVisibility(state, confirmationModal) {
    state.confirmationModal.visibility = confirmationModal.visibility;
    state.confirmationModal.data = confirmationModal.data;
  },
  updateMyFavouritesParams(state, params) {
    const updatedPageParam = params.page
      ? {}
      : {
          page: 1,
        };
    state.myFavourites.params = {
      ...state.myFavourites.params,
      ...params,
      ...updatedPageParam,
    };
  },
  updateSuccessModal(state, data) {
    state.successModalVisibility = data.successModalVisibility;
  },
  updatePropertyReviewLoading(state, loading) {
    state.property.reviewLoading = loading;
  },
  setSingleOfferDetails(state, offerDetails) {
    state.offerDetails = offerDetails;
  },
  updateOfferDetailsLoading(state, loading) {
    state.offerDetailsLoading = loading;
  },
  setSelectedOfferId(state, offerId) {
    state.selectedOfferId = offerId;
  },
  setRejectionReasons(state, payload) {
    state.rejectionReasons = payload;
  },
  setTenantReviewNotes(state, payload) {
    state.tenantReviewNotes = payload;
  },
  setTenantReviewLoading(state, loading) {
    state.tenantReviewLoading = loading;
  },
  updatePropMasterData(state, propMasterData) {
    state.propMasterData = propMasterData;
  },
  updateHotelType(state, hotelType) {
    state.hotelType = hotelType;
  },
  updateChildRoot(state, payload) {
    switch (payload.type) {
      case 'district':
        state.districtLocation = payload.response.sort((a, b) =>
          a.location_name.toLowerCase() > b.location_name.toLowerCase() ? 1 : -1
        );
        break;
      case 'neighborhood':
        state.isNeighborhoodLocationLoaded = false;
        state.neighborhoodLocation = payload.response.sort((a, b) =>
          a.location_name.toLowerCase() > b.location_name.toLowerCase() ? 1 : -1
        );
        state.isNeighborhoodLocationLoaded = true;
        break;
    }
  },
  updateIndicator(state, indicator) {
    state.indicator = indicator;
  },
  updatePropertySearchParams(state, payload) {
    const updatedPageParam = payload.page ? {} : { page: 1 };
    state.filteredProperty.params = {
      ...state.filteredProperty.params,
      ...payload,
      ...updatedPageParam,
    };
  },
  resetFilterData(state) {
    state.filteredProperty.params = {
      search_text: null,
      term: 0,
      page: 1,
      perPage: 21,
      sort: 'plh',
      price: [0, 100000],
      filter: false,
      bath: 0,
      bed: 0,
      pt: [],
      amenity: [],
      inCompound: 0,
    };
  },
  listSearchedProperty(state, payload) {
    state.filteredProperty.data = payload;
    state.filteredProperty.total = payload.count;
  },
  updatePropertySearchLoading(state, loading) {
    state.filteredProperty.loading = loading;
  },
  setTerm(state, term) {
    state.term = term;
  },
  changeSortValue(state, sort) {
    state.filteredProperty.params.sort = sort;
  },
  activeCmpareModal(state, val) {
    state.openCmpareModal = val;
  },
  amenities(state, amenities) {
    state.bathRoomAmenities = amenities.bathRoomAmenities;
    state.kitchenAmenities = amenities.kitchenAmenities;
    state.propertyAmenities = amenities.propertyAmenities;
  },
  propertyAmenities(state, propertyAmenities) {
    state.propertyAmenities = propertyAmenities;
  },
  uploadedFile(state, images) {
    state.uploadedFile = images;
  },
  listCompareProperty(state, compareProperty) {
    state.compareProperty = compareProperty;
  },
  clearCompareProperty(state) {
    state.compareProperty = [];
  },
  updateOfferStatus(state, status) {
    state.offerStatus = status;
  },
  updateRespondActionLoading(state, payload) {
    state.respondActionLoading.loading = payload.status;
    state.respondActionLoading.type = payload.type;
  },
  updateRespondActionVisibility(state, payload) {
    state.respondActionModalVisibility.visibility = payload.visibility;
    state.respondActionModalVisibility.type = payload.type;
  },
  updateRejectionReason(state, rejection_reason) {
    state.rejectionReason = rejection_reason;
  },
  setRangePrice(state, price) {
    state.rangePrice = [price.min, price.max];
  },
  setSimilarLocations(state, similarLocations) {
    state.similarLocations = similarLocations;
  },
  updatePropertyOpen(state, payload) {
    switch (payload.type) {
      case 'propertyDetails':
        state.propertyDetails.property.status = payload.status;
        break;
      case 'myProperties':
        const propertyIndex = state.myPropertiesPage.data.findIndex(
          (property) => property.id == payload.id
        );
        if (propertyIndex != -1) {
          this._vm.$set(state.myPropertiesPage.data, propertyIndex, {
            ...state.myPropertiesPage.data[propertyIndex],
            status: payload.status,
          });
        }
        break;
    }
  },
  setRelated(state, related) {
    state.related = related;
  },
  addBid(state, id) {
    state.bidId = id;
  },
  prviewFurnish(state, furnishText) {
    state.furnishText = furnishText;
  },
  listOldPropertyData(state, oldPropertyData) {
    state.oldPropertyData = oldPropertyData;
  },
  changeOldPropertyImages(state, payload) {
    state.oldPropertyData.old_images = payload;
  },
  setLandLordReviews(state, tenantReview) {
    const reviewObject = {};
    let reviews = JSON.parse(JSON.stringify(tenantReview));
    let reformattedArray = reviews.map((obj) => {
      if (!obj['rating']) obj['rating'] = 0;
      return obj;
    });
    reviewObject['note'] = '';
    reviewObject['review'] = reviews;
    state.reviews.reviews = [reviewObject];
  },
  updateAddEditReviewModalVisibility(state, visibility) {
    state.addEditReviewModal.visibility = visibility;
  },
  updateAddEditReviewModalLoading(state, loading) {
    state.addEditReviewModal.loading = loading;
  },
  updateAddEditReviewButtonLoading(state, loading) {
    state.addEditReviewButtonLoading = loading;
  },
  setTenantReviews(state, reviews) {
    const reviewObject = {};
    let tmpReviews = JSON.parse(JSON.stringify(reviews));
    let reformattedArray = tmpReviews.map((obj) => {
      if (!obj['rating']) obj['rating'] = 0;
      return obj;
    });
    reviewObject['note'] = '';
    reviewObject['review'] = tmpReviews;
    state.reviews.reviews.push(reviewObject);
  },
  resetTenantReviews(state) {
    state.reviews.reviews = [];
  },
  updateReviews(state, payload) {
    state.reviews.reviews[payload.index].review = payload.reviews;
  },
  setReviewsNote(state, payload) {
    state.reviews.reviews[payload.index].note = payload.note;
  },
  setServerErrors(state, errors) {
    state.serverErrors = errors;
  },
  updateReviewModalVisibility(state, visibility) {
    state.reviewModal.visibility = visibility;
  },
  updateReviewModalLoading(state, loading) {
    state.reviewModal.loading = loading;
  },
  setReviewQuestions(state, questions) {
    state.reviewModal.data.reviews = questions;
  },
  SET_ACTIVE_CHAT_FRIEND(state, payload) {
    state.activeFriend = payload;
  },
  SET_POST_REQUEST_FORM_SUBMITTED(state) {
    state.postRequestFormSubmitted = true;
  },
  SET_PROPERTY_IMAGES(state, image) {
    if (image?.length === 0) return (state.propertyImages = []);
    state.propertyImages.push(image);
  },
  SET_SORTED_PROPERTIES(state, sortedProperties) {
    state.sortedProperties = sortedProperties;
  },
  DELETE_PROPERTY_IMAGE(state, index) {
    state.propertyImages.splice(index, 1);
  },
  SET_UPDATED_PROPERTY(state, property) {
    state.updatedProperty = property;
  },
  set_clickedPropToAttachOwner(state, prop){
    state.clickedPropToAttachOwner = prop;
  },
  set_createdPropertyId(state, id){
    state.createdPropertyId = id
  },
  set_createdPropertyId_Null(state){
    state.createdPropertyId = null
  },
};
