import { authenticationToken } from '@/helpers/constants/auth'

export const actions = {
  async nuxtServerInit({ dispatch }, { app: { $cookies } }) {
    if ($cookies.get(authenticationToken)) {
      await dispatch('UserModule/getUserProfile')
      await dispatch('AuthModule/setToken', $cookies.get(authenticationToken))
      await dispatch('UserModule/getNotifcation')
    }
  },
}
