import { guestUserStorage, authenticationToken } from '@/helpers/constants/auth'

export default {
  startAuthLoading(state) {
    state.authLoading = true
  },
  startFacebookAuthLoading(state) {
    state.facebookAuthLoading = true
  },
  startGoogleLoading(state) {
    state.googleAuthLoading = true
  },
  endAuthLoading(state) {
    state.authLoading =
      state.facebookAuthLoading =
      state.googleAuthLoading =
        false
  },
  startForgetPasswordLoading(state) {
    state.forgetPasswordLoading = true
  },
  endForgetPasswordLoading(state) {
    state.forgetPasswordLoading = false
  },
  setUserToken(state, authToken) {
    this.$cookies.set(authenticationToken, `${authToken}`)

    state.token = authToken
  },
  removeUserToken(state) {
    this.$cookies.remove(authenticationToken)
    window.localStorage.clear()
    location.reload()
  },
  updateAuthModalVisibility(state, authModal) {
    state.authModal.visibility = authModal.visibility
    state.authModal.callbackAction = authModal.callbackAction
  },
  setServerErrors(state, payload) {
    state.serverErrors = payload
  },
  setSuccessResponse(state, payload) {
    state.successResponse = payload
  },

}
