import API from './api'

export default {
  getUserProfile({ commit, dispatch }, payload) {
    commit('setLoader', true)
    return API.getUserProfile().then((res) => {
      commit('setUserData', res)
      commit('setLoader', false)

      return payload
    })
  },
  getNotifcation({ commit }) {
    commit('setNotificationLoader', true)
    return API.getNotifcation().then((res) => {
      commit('updateNotification', res.Data)
      commit('setNotificationLoader', false)
    })
  },
  markAllAsRead({ dispatch }) {
    return API.markAllAsRead().then(() => {
      dispatch('getNotifcation')
    })
  },
  compareOffers({ commit }, payload) {
    if (payload.length > 0) {
      return API.compareOffers(payload).then((res) => {
        commit('compareOffers', res.Data)
      })
    } else {
      commit('compareOffers', [])
    }
  },
  saveUserProfile(context, payload) {
    context.commit('setLoader', true)
    return API.saveUserProfile(payload)
      .then((res) => {
        context.commit('setServerErrors', {})
        context.commit('setResponse', res)
      })
      .catch((err) => {
        if (err.response) {
          context.commit('setServerErrors', err.response.data)
        }
      })
      .finally(() => {
        context.commit('setLoader', false)
      })
  },
  getNationalites(context) {
    return API.getNationalites().then((res) => {
      context.commit('setNationalites', res.Data)
    })
  },
  resetUserUpdateResponse({ commit }) {
    commit('RESET_RESPONSE')
  },
  enableCreateMode({commit}){
    commit('Enable_Create_Mode')
  },
  disableCreateMode({commit}){
    commit('Disable_Create_Mode')
  },
  Set_editOwnerId({commit}, id){
    commit('Set_editOwnerId', id)
  }
}
