import API from './api'

export default {
  // friends list
  getFriends({ commit, state }, payload) {
    commit('setLoader', {
      loader: 'loadingFriends',
      value: true,
    })
    return API.getFriends(
      payload ? (payload.params ? payload.params : null) : null
    )
      .then((res) => {
        commit('setFriendsMeta', res)
        commit('setFriends', {
          friends: res.data,
          recentSender: payload
            ? payload.recentSender
              ? payload.recentSender
              : null
            : null,
          recentSenderObj: payload
            ? payload.recentSenderObj
              ? payload.recentSenderObj
              : null
            : null,
          filterProperty: payload
            ? payload.filterProperty
              ? payload.filterProperty
              : null
            : null,
        })
      })
      .finally(() => {
        commit('setLoader', {
          loader: 'loadingFriends',
          value: false,
        })
      })
  },
  loadFriendsData({ dispatch, state }, payload) {
    return Promise.all([dispatch('getFriends', payload)])
  },

  getFriendsProperties({ commit, state }) {
    return API.getFriendsProperties().then((res) => {
      commit('setFriendsProperties', res.Data)
    })
  },
  // single friend messages
  getMessages({ commit, state }, payload) {
    commit('setLoader', {
      loader: 'loadingMessages',
      value: true,
    })

    return API.getMessages(
      payload.session_id,
      payload.property_id,
      payload.params
    )
      .then((res) => {
        commit('setMessagesMeta', res.Data)
        commit('setMessages', {
          messages: res.Data.data,
          lastMessage: payload.lastMessage ? true : false,
        })
      })
      .finally(() => {
        commit('setLoader', {
          loader: 'loadingMessages',
          value: false,
        })
        commit('setLoader', {
          loader: 'sendingMessageLoader',
          value: false,
        })
      })
  },
  loadMessages({ dispatch }, payload) {
    return Promise.all([dispatch('getMessages', payload)])
  },

  // send messages
  sendMessageHandler({ commit, state }, payload) {
    commit('setLoader', {
      loader: 'sendingMessageLoader',
      value: true,
    })

    return API.sendMessage(payload.messageObj, payload.session_id)
      .then((res) => {
        console.log(' ~ file: actions.js ~ line 95 ~ .then ~ res', res)
        console.log(payload.messageObj)
        // commit('setMessage', {
        //   message: res.Data,
        //   messageObj: payload.messageObj,
        // })
      })
      .finally(() => {
      })
  },
  sendMessage({ dispatch }, payload) {
    return Promise.all([dispatch('sendMessageHandler', payload)])
  },

  // activate chat
  activateChat({ commit }, payload) {
    commit('activateChat', payload)
  },

  // read recent message
  // activate chat
  readRecentMessage({ commit, dispatch, state }, payload) {
    return Promise.all([
      API.readRecentMessage(payload.session_id, payload.property_id),
    ]).then(() => {
      commit('setFriendUnreadMessages', {
        id: payload.friend_id,
        session_id: payload.session_id,
      })
    })
  },
  fetchSingleFriend({ commit, state, dispatch }, payload) {
    commit('setLoader', {
      loader: 'loading',
      value: true,
    })
    return API.getSingleFriends(payload).then((res) => {
      commit('setFriends', {
        friends: [],
        recentSender:
          payload && payload.recentSender ? payload.recentSender : true,
        recentSenderObj: [res.Data],
      })

      dispatch('activateChat', {
        session_id: res.Data.session.id,
        property_id: res.Data.property_id,
        friend_id: res.Data.id,
      })

      dispatch('loadMessages', {
        session_id: res.Data.session.id,
        property_id: res.Data.property_id,
        friend_id: res.Data.id,
      })

      commit('setLoader', {
        loader: 'loading',
        value: false,
      })
    })
  },
  getSingleProperty({ commit, state }, payload) {
    commit('setLoader', {
      loader: 'loadingProperty',
      value: true,
    })
    return API.getSingleProperty(payload).then((res) => {
      commit('setSingleProperty', res.Data)
      commit('setLoader', {
        loader: 'loadingProperty',
        value: false,
      })
    })
  },
  resetSingleProperty({ commit }) {
    commit('setSingleProperty', {})
  },
  getSingleOffer({ commit, state }, payload) {
    commit('setLoader', {
      loader: 'loadingOffer',
      value: true,
    })
    return API.getSingleOffer(payload).then((res) => {
      commit('setSingleOffer', res.Data)
      commit('setLoader', {
        loader: 'loadingOffer',
        value: false,
      })
    })
  },
}
