import Vue from 'vue'

export default ({ store: { state } }) => {
  const locale = state.i18n.locale
  Vue.filter('formatDate', function (date) {
    if (!date) {
      return ''
    }
    const enDate = new Intl.DateTimeFormat(
      locale === 'ar' ? 'ar-EG' : 'en-US',
      {
        year: 'numeric',
        month: 'short',
        day: '2-digit',
      }
    )
    const formatParts = enDate.formatToParts(new Date(date))
    const formatPartsObject = formatParts.reduce((partsObj, part) => {
      partsObj[part.type] = part.value
      return partsObj
    }, {})
    return `${formatPartsObject.day} ${formatPartsObject.month}. ${formatPartsObject.year}`
  })

  Vue.filter('displayExcerpt', function (str, length) {
    if (str.length > length) {
      return str.substring(0, length) + '...'
    }
    return str
  })
}
