<!-- profile menu page message -->
<template>
  <div class="profile-menu">
    <b-dropdown
      right
      variant="link"
      toggle-class="text-decoration-none d-flex align-items-center"
      no-caret>
      <!-- profile user btn  -->
      <template #button-content>
        <img :class="['mx-1', !isTransparent && isHomePage ? 'd-none' : '']" width="18px" height="18px" :src="require('@/assets/images/sidebar/User-black.svg')" alt="White User">
        <img :class="['mx-1', !isTransparent && isHomePage ? '' : 'd-none']" width="18px" height="18px" :src="require('@/assets/images/sidebar/User-white.svg')" alt="Black User">
        <span class="d-mobile-none">{{ user.full_name }}</span>
      </template>
      <!-- profile btn drop down menu -->
      <b-dropdown-item to="/profile">
        <div class="text">
          <img
            width="20px"
            height="20px"
            class="mr-1"
            :src="require('@/assets/images/sidebar/User.svg')" />
          {{ $t('header.my_profile') }}
        </div>
        <img
          width="16px"
          height="16px"
          class="arrow-icon"
          :src="require('@/assets/images/sidebar/CaretRight.svg')" />
      </b-dropdown-item>
      <!-- logout btn from drop down menu -->
      <b-dropdown-item @click="$emit('logout')">
        <div class="text logout">
          <img
            width="20px"
            height="20px"
            class="mr-1"
            :src="require('@/assets/images/sidebar/SignOut.png')" />
          {{ $t('header.logout') }}
        </div>
      </b-dropdown-item>
    </b-dropdown>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
const { mapState } = createNamespacedHelpers('UserModule');
const { mapGetters: mapCoreGetters } = createNamespacedHelpers('CoreModule');
export default {
  computed: {
    ...mapState(['user']),
    ...mapCoreGetters(['isRTL']),
    isHomePage() {
      return this.$route?.name?.includes('index');
    },
  },
  props:{
    isTransparent: {
      default: true
    }
  }
};
</script>

<style lang="scss">
.profile-menu {
  .dropdown {
    button.btn-link {
      padding: 0;
    }

    ul.dropdown-menu {
      width: 260px;
      padding: 20px;
      border-radius: 12px;
      border: 1px solid #f2f4f5;

      li {
        margin-bottom: 16px;
        a {
          display: flex;
          justify-content: space-between;
          align-items: center;
          font-size: 14px;
          font-weight: bold;
          color: #111 !important;
          padding: 10px 12px;
          border: 1px solid #f2f4f5;
          border-radius: 8px;
        }

        &:last-child {
          margin: 0;
        }

        .logout {
          color: #ff5252;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .profile-menu {
    .d-mobile-none {
      display: none;
    }
  }
}
</style>
