<template>
  <div class="user-menu">
    <transition name="slide-down">
      <!-- i am tenant drop down menu  -->
      <b-dropdown
        variant="link"
        toggle-class="text-decoration-none font-bold px-2"
        style="border: 2px solid #fff;border-radius:5px"
        no-caret
        :text="menu.tenant.title"
        class="mx-3 font-bold">
        <div class="menu">
          <div class="container">
            <!-- left side of tenant menu  -->
            <div class="col-6">
              <!-- text of menu -->
              <div class="text font-bold">
                <h3>{{ menu.tenant.title }}</h3>
                <p>
                  {{ menu.tenant.subtitle }}
                </p>
              </div>
              <!-- post a new request menu -->
              <div class="actions">
                <MenuAction
                  v-for="(item, index) in menu.tenant.actions"
                  :key="index"
                  :item="item" />
              </div>
            </div>
            <!-- right side of tenant menu  -->
            <div class="col-6 tenant">
              <!-- dahboard title -->
              <div class="text">
                <h3>{{ $t('home.my_dashboard') }}</h3>
                <!-- <p>
                  {{ $t('home.my_dashboard_details') }}
                </p> -->
              </div>
              <!-- if you are loged in show menu action -->
              <div class="actions" v-if="user">
                <MenuAction
                  v-for="(item, index) in menu.tenant.dashboardItems"
                  :key="index"
                  :item="item" />
              </div>
              <!-- if you aren't loged in show login btn and register -->
              <div v-else>
                <p class="text-light">{{ $t('home.login_message') }}</p>
                <div>
                  <b-button class="bg-light text-dark" @click="showAuthModal">
                    <!-- in line style customize -->
                    <span class="d-mobile-none" id="login-btn-menu" style="color:black; font-size: 19px; font-weight: bold; ">{{ $t('header.login') }}</span>
                  </b-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </b-dropdown>
    </transition>
      <!-- i am landloard drop down menu  -->
    <b-dropdown
      variant="link"
      toggle-class="text-decoration-none font-bold px-2"
      style="border: 2px solid #fff;border-radius:5px"
      no-caret
      :text="menu.landlord.title">
      <transition name="slide-down">
        <div class="menu">
          <div class="container">
            <!-- left side of landloard menu  -->
            <div class="col-6">
              <!-- text of menu -->
              <div class="text">
                <h3>{{ menu.landlord.title }}</h3>
                <p>
                  {{ menu.landlord.subtitle }}
                </p>
              </div>
              <!-- post a new request menu -->
              <div class="actions">
                <MenuAction
                  v-for="(item, index) in menu.landlord.actions"
                  :key="index"
                  :item="item" />
              </div>
            </div>
             <!--right side of landlord menu -->
            <div class="col-6 landlord">
              <!-- post a new request menu -->
              <div class="text">
                <h3>{{ $t('home.my_dashboard') }}</h3>
                <!-- <p>
                  {{ $t('home.my_dashboard_details') }}
                </p> -->
              </div>
              <!-- if you are loged in show menu action -->
              <div class="actions" v-if="user">
                <MenuAction
                  v-for="(item, index) in menu.landlord.dashboardItems"
                  :key="index"
                  :item="item" />
              </div>
              <!-- if you aren't loged in show login btn and register -->
              <div v-else>
                <p class="text-dark">{{ $t('home.login_message') }}</p>
                <div>
                  <!-- giving id to btn to customize -->
                  <b-button class="bg-light text-dark" @click="showAuthModal" id="login-btn-menu" style="background-color: #0078ff;color: #fff !important;border: none;">
                    <!-- in line style customize -->
                    <span class="d-mobile-none" style="color:black; font-size: 19px; font-weight: bold; ">{{ $t('header.login') }}</span>
                  </b-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </transition>
    </b-dropdown>
  </div>
</template>

<script>
// import MenuAction from folder app header
import MenuAction from './MenuAction';
import { createNamespacedHelpers } from 'vuex'
const { mapState: mapUserState } = createNamespacedHelpers('UserModule')
const { mapActions: mapAuthActions } = createNamespacedHelpers('AuthModule');

export default {
  components: {
    MenuAction,
  },
  data() {
    return {
      active: false,
      tenantMenu: false,
      landlordMenu: false,
      menu: {
        tenant: {
          title: this.$t('header.i_tenant'),
          subtitle:
            this.$t('home.my_dashboard_details'),
          actions: [
            // {
            //   title: this.$t('header.explore'),
            //   link: '/properties',
            //   type: 'outlined',
            //   icon: 'Binoculars.svg',
            // },
            {
              title: this.$t('header.post_new_request'),
              link: '/requests/post/property-category',
              type: 'primary',
              icon: null,
            },
          ],
          dashboardItems: [
            {
              title: this.$t('header.myRequests'),
              link: '/dashboard/requests',
              type: 'outlined',
              icon:'post-request.svg',
            },
            {
              title: this.$t('header.myFavorites'),
              link: '/dashboard/favorite-properties',
              type: 'outlined',
              icon: 'Heart.svg',
            },
            {
              title: this.$t('header.myInbox'),
              link: '/dashboard/messaging',
              type: 'outlined',
              icon: 'EnvelopeSimple.svg',
            },
          ],
        },
        landlord: {
          title: this.$t('header.i_landloard'),
          subtitle:
            this.$t('home.my_dashboard_text'),
          actions: [
            {
              title: this.$t('marketplace.title'),
              link: '/marketplace',
              type: 'outlined',
              icon: 'Storefront.svg',
            },
            {
              title: this.$t('header.listMyProp'),
              link: '/properties/add/1',
              type: 'primary',
              icon: null,
            },
          ],
          dashboardItems: [
            {
              title: this.$t('header.myOffers'),
              link: '/dashboard/offers?type=public',
              type: 'outlined',
              icon:  'offer.svg',
            },
            {
              title: this.$t('header.my_properties'),
              link: '/dashboard/my-properties',
              type: 'outlined',
              icon: 'property.svg',
            },
            {
              title: this.$t('header.myInbox'),
              link: '/dashboard/messaging',
              type: 'outlined',
              icon: 'EnvelopeSimple.svg',
            },
          ],
        },
      },
    };
  },
  methods: {
    ...mapAuthActions(['showAuthModal']),
    toggleMenu(type) {
      if (type === 'tenant') {
        this.tenantMenu = !this.tenantMenu;
        this.landlordMenu = false;
      } else {
        this.landlordMenu = !this.landlordMenu;
        this.tenantMenu = false;
      }
    },
  },
  beforeDestroy() {
    this.tenantMenu = false;
    this.landlordMenu = false;
  },
  computed:{
    ...mapUserState(['user']),
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  },
};
</script>

<style lang="scss">
#login-btn-menu{
  color: black ;
}
.user-menu {
  display: flex;
  .py {
    padding-top: 15px !important;
    padding-bottom: 15px !important;
  }
  .dropdown-menu {
    padding: 0;
    background: transparent;
    border: none;

    .menu {
      background-color: #fff;
      width: 700px;
      border-radius: 20px;
      border: 1px solid #f2f4f5;
      box-shadow: 0 3px 6px 0 RGB(0 0 0 / 10%);
      &.notUser {
        width: 400px;
      }
      .container {
        padding: 0;
        display: flex;
        align-items: center;

        .col-6 {
          padding: 32px;
          height: 100%;

          .text {
            h3 {
              color: #111;
              font-weight: bold;
              font-size: 16px;
              margin-bottom: 16px;
            }

            p {
              color: #48535b;
              line-height: 1.5;
              font-weight: lighter;
            }
          }

          &:last-child {
            border-radius: 0 20px 20px 0;

            &.tenant {
              background-color: #004ed4;

              .text {
                h3,
                p {
                  color: #fff;
                }
              }
            }

            &.landlord {
              background-color: #ffc025;
              padding: 51px;
              height: 100%;
              .text {
                h3,
                p {
                  color: #111;
                }
              }
            }
          }
        }
      }
    }
  }
}

.slide-down-enter {
  transform: translateY(-10px);
  opacity: 0;
}

.slide-down-enter-active,
.slide-down-leave-active {
  transition: all 0.3s ease;
}

.slide-down-leave-to {
  transform: translateY(-10px);
  opacity: 0;
}

@media screen and (max-width: 767px) {
  .user-menu {
    display: none;
  }
}
</style>
