export default {
  setUserData(state, user) {
    state.user = user.Data
  },
  updateNotification(state, allNotification) {
    state.allNotification = allNotification
  },
  compareOffers(state, compareOffers) {
    state.compareOffers = compareOffers
  },
  setLoader(state, value) {
    state.loading = value
  },
  setNationalites(state, data) {
    state.nationalites = data
  },
  setServerErrors(state, data) {
    state.serverErrors = data ? data.Data : null
  },
  setResponse(state, data) {
    state.response = data
  },
  setNotificationLoader(state, value) {
    state.notificationLoading = value
  },
  RESET_RESPONSE(state) {
    state.response = null
  },
  Enable_Create_Mode(state){
    state.isCreateMode = true;
    localStorage.setItem('isCreateMode', JSON.stringify(true))
  },
  Disable_Create_Mode(state){
    state.isCreateMode = false;
    localStorage.setItem('isCreateMode', JSON.stringify(false))
  },
  Set_editOwnerId(state, id){
    state.editOwnerId = id;
  }
}
