import API from './api'

export default {
  getFavouriteCities({ commit }) {
    return API.getFavouriteCities().then((res) => {
      commit('setFavouriteCities', res)
    })
  },
  loadHomeData({ dispatch }) {
    return Promise.all([dispatch('getFavouriteCities')])
  },
  getPrivacy({ commit }) {
    return API.getPrivacy().then((res) => {
      return res
    })
  },
  getTerms({ commit }) {
    return API.getTerms().then((res) => {
      return res
    })
  },
  getSuggestions({ commit }, payload) {
    commit('setLoader', true)
    return API.getSuggestions(payload).then((res) => {
      commit('setSuggestions', res.Data)
      commit('setLoader', false)
    })
  },
  clearSuggestions({ commit }) {
    commit('CLEAR_SUGGESTIONS')
  },
  Set_View({ commit }, view){
    commit('Set_View', view)
  },
}
