<template>
  <div>
    <div class="upperHeader bg-primary text-white py-2 text-center px-2">RentUp Beta Version Now LIVE! SignUp to try our services</div>
    <div :class="{
      'transparent-header': isTransparentHeader,
      'no-padding-top': isHomePage,
    }">
      <AppHeader class="d-none d-md-block" />
      <!-- <AppHeaderNew /> -->
  
      <!-- @successGoogleLogin="show = true" -->
      <AuthModal />
      <!-- <you-broker :show="show" @closeBrokerModal="show = false" /> -->
      <div :class="[isHomePage ? 'main-content-container overflow-hidden home' : 'main-content-container overflow-hidden', isPostReqOrAdd ? 'no_marginBottom' : '']">
        <Nuxt />
      </div>
      <AppFooter v-if="showHeaderAndFooter" />
      <LowerNav />
      <noscript v-if="this.$config.APP_ENV == 'production'"><iframe
          src="https://www.googletagmanager.com/ns.html?id=GTM-5Z3VS3H" height="0" width="0"
          style="display: none; visibility: hidden"></iframe></noscript>
      <SendOfferModal />
      <!--    <navigationBar />-->
      <CustomTour cookie_name="home_tour_finished" :steps="steps" />
    </div>
  </div>
</template>

<script>
import AppHeader from '@/components/shared/AppHeader';
import AppHeaderNew from '@/components/shared/AppHeaderNew';
import AppFooter from '@/components/shared/AppFooter';
import AuthModal from '@/components/shared/AuthModal';
import navigationBar from '@/components/navigationBar';
import SendOfferModal from '~/components/favorite-properties/SendOfferModal';
import LowerNav from '@/components/onboarding/LowerNav.vue'
// import youBroker from '@/components/shared/form/youBroker.vue'

export default {
  components: {
    AppHeader,
    AppHeaderNew,
    AppFooter,
    AuthModal,
    SendOfferModal,
    navigationBar,
    LowerNav,
    // youBroker,
  },
  head() {
    return this.$nuxtI18nHead({ addSeoAttributes: true });
  },
  key(route) {
    return route.fullPath;
  },
  data() {
    return {
      scrollEventDebounce: null,
      isTransparentHeader: false,
      scrollEventActivated: false,
      showHeaderAndFooter: true,
      // show: false,
    };
  },
  computed: {
    isHomePage() {
      return this.$route?.name?.includes('index');
    },
    isPropertyListing() {
      return this.$route?.name?.includes('properties-add');
    },
    userID() {
      return this.$store.state.UserModule.user;
    },

    steps() {
      return [
        // {
        //   target: '.v-step-1',
        //   content: `<div>
        //   <p>1/2</p>
        //   <p>${this.$t('home.first_step_tour')}</p>
        //   </div>`,
        // },
        {
          target: '#v-step-2',
          content: `<div>
          <p>${this.$t('home.second_step_tour')}</p>
          </div>`,
          params: {
            enableScrolling: false,
          },
        },
      ];
    },
    isPostReqOrAdd() {
      return this.$route?.path?.includes('requests/post') || this.$route?.path?.includes('properties/add');
    },
  },
  methods: {
    handleScrollEvent() {
      clearTimeout(this.scrollEventDebounce);
      this.scrollEventDebounce = setTimeout(() => {
        if (window.scrollY > 100) {
          this.isTransparentHeader = false;
        }
      }, 150);
    },
    addScrollEvent() {
      window.addEventListener('scroll', this.handleScrollEvent);
    },
    removeScrollEvent() {
      window.removeEventListener('scroll', this.handleScrollEvent);
    },
    handleOnScroll(path) {
      if (typeof window === 'undefined') return;
      if (this.isHomePage) {
        !this.scrollEventActivated && this.addScrollEvent();
      } else {
        this.removeScrollEvent();
      }
    },
  },
  mounted() {
    window.addEventListener('resize', () => {
      if (
        this.$route?.name?.includes('properties-add') ||
        this.$route?.name?.includes('properties-edit')
      )
        if (window.innerWidth <= 767) {
          this.showHeaderAndFooter = false;
        } else {
          this.showHeaderAndFooter = true;
        }
    });
    this.handleOnScroll(this.$route.path);
    // this.handleNotification()
    // this.$store.dispatch('UserModule/getNotifcation')
  },
  watch: {
    $route(to) {
      this.handleOnScroll(to.path);
    },
    $i18n: {
      handler(value) {
        if (process.client) {
          if (this.$i18n.locale === 'ar') {
            document.body.lang = 'ar';
            document.body.dir = 'rtl';
          } else {
            document.body.lang = 'en';
            document.body.dir = 'ltr';
          }
        }
      },
      immediate: true,
    },
  },
  head() {
    return {
      ...this.$nuxtI18nHead({ addSeoAttributes: true }),
      htmlAttrs: {
        lang: this.$i18n.locale,
      },
      meta: [

      ],
      script: [
        this.$config.APP_ENV == 'production' ? {
          hid: 'google-manager',
          type: 'text/javascript',
          innerHTML:
            "(function(w,d,s,l,i){w[l]=w[l]||[];w[l].push({'gtm.start':new Date().getTime(),event:'gtm.js'});var f=d.getElementsByTagName(s)[0],j=d.createElement(s),dl=l!='dataLayer'?'&l='+l:'';j.async=true;j.src='https://www.googletagmanager.com/gtm.js?id='+i+dl;f.parentNode.insertBefore(j,f);})(window,document,'script','dataLayer','GTM-5Z3VS3H');",
        } : {},
        {
          hid: 'facebook-sdk',
          type: 'text/javascript',
          innerHTML: `
           window.fbAsyncInit = function () {
        FB.init({
          cookie: true,
          xfbml: true,
          version: 'v10.0',
        })

        FB.AppEvents.logPageView()
      }

      ;(function (d, s, id) {
        var js,
          fjs = d.getElementsByTagName(s)[0]
        if (d.getElementById(id)) {
          return
        }
        js = d.createElement(s)
        js.id = id
        js.src = 'https://connect.facebook.net/en_US/sdk.js'
        fjs.parentNode.insertBefore(js, fjs)
      })(document, 'script', 'facebook-jssdk')
           `,
        },
      ],
      __dangerouslyDisableSanitizersByTagID: {
        'google-manager': ['innerHTML'],
        'google-analytics': ['innerHTML'],
        'facebook-sdk': ['innerHTML'],
      },
    };
  },
};
</script>

<style lang="scss">
.upperHeader {
  font-size: 13px !important;
  @media screen and (max-width: 400px){
    font-size: 11px !important;
  }
}
// @import url('https://fonts.googleapis.com/css2?family=Mulish:wght@300;400;700&display=swap');
body[dir='ltr'], body[dir='ltr'] *{
  font-family: 'Inter', sans-serif !important;
}

body[dir='rtl'], body[dir='rtl'] *{
  font-family: 'Cairo', sans-serif !important;
}
.main-content-container {
  min-height: calc(100vh - 274px);
  /* fix footer issue with pages that not have enough content */
  /* header padding */
  // margin-bottom: 0 !important;
  background: #fbfbfb;
  @media(max-width: 767px){
    margin-bottom: 70px !important;
  }
  @media(min-width: 992px){
    padding-top: 60px;
  }
  &.no_marginBottom {
    margin-bottom: 0 !important;
  }
  .no-padding-top & {
    @include media-breakpoint-up(lg) {
      padding-top: 0;
    }
  }
}
</style>
