import $axios from '@/helpers/axios-port'
export default {
  getFavouriteCities() {
    return $axios.$get('/getFavouriteCities').then((res) => res.Data)
  },
  getTerms() {
    return $axios.$get('/getTerms')
  },
  getPrivacy() {
    return $axios.$get('/getPrivacy')
  },
  getSuggestions({ keyword, term }) {
    return $axios.$get(`/autoComplete?keyword=${keyword}&term=${term}`)
  },
}
