<template>
  <header class="app-header" ref="header" :class="dark && 'dark-header'">
    <b-navbar class="app-header-navbar" toggleable="lg">
      <b-container class="app-header-container">
        <svg-icon
          class="back"
          @click="changeprevious"
          name="back"
          height="24px"
        />
      </b-container>
    </b-navbar>
  </header>
</template>

<script>
import HeaderNav from './HeaderNav'

export default {
  components: {
    HeaderNav,
    SearchField: () => import('@/components/home/SearchField.vue'),
    SideBar: () => import('./SideBar.vue'),
  },
  props: {
    dark: {
      type: Boolean,
      default: false,
    },
  },
  directives: {
    scroll: {
      bind(el) {
        if (window.innerWidth >= 768) {
          el.style.opacity = '1'
          el.style.visibility = 'visible'
          return
        }
        document.addEventListener('scroll', (e) => {
          if (window.innerWidth <= 767) {
            if (window.scrollY > 50) {
              el.style.opacity = '1'
              el.style.visibility = 'visible'
            } else {
              el.style.opacity = '0'
              el.style.visibility = 'hidden'
            }
          }
        })
      },
    },
  },
  data() {
    return {
      disabledBtn: true,
      visible: false,
      mobileDevice: false,
      showSideBar: false,
    }
  },
  methods: {
    toggleHeader() {
      if (window.innerWidth <= 992) {
        this.$refs.header.classList.toggle('active-header')
        // this.$refs.whiteLogo.classList.toggle('d-none')
        // this.$refs.defaultLogo.classList.toggle('d-block')
      }
    },
    onScroll() {
      this.windowTop = window.top.scrollY
      if (this.windowTop > 300) {
        this.visible = false
        this.$refs.header.classList.remove('active-header')
        // this.$refs.whiteLogo.classList.remove('d-none')
        // this.$refs.defaultLogo.classList.remove('d-block')
      }
    },
    changeprevious() {
      this.$nuxt.$emit('previousStep')
    },
  },
  mounted() {
    this.disabledBtn = false
    if (process.browser) {
      window.addEventListener('scroll', this.onScroll)
      window.addEventListener('resize', () => {
        if (window.innerWidth >= 768) {
          this.$refs.header.classList.add('show-header')
        } else {
          this.$refs.header.classList.remove('show-header')
        }
      })
    }
  },
  computed: {
    currentRoute() {
      return this.$route.path
    },
  },
  watch: {
    currentRoute(newVal, oldval) {
      if (newVal != oldval) {
        this.visible = false
        this.$refs.header.classList.remove('active-header')
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.app-header {
  transition: all 0.5s ease-in-out;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1000;
  background-color: #fff;
  &.dark-header {
    background-color: $mainColorDark;
  }
  .white-logo {
    display: none;
  }
  .navbar-toggler {
    background-color: #fff;
    padding: 0.15rem 0.5rem;
  }
  .transparent-header & {
    background-color: transparent;
    color: #fff;
    box-shadow: none;
    .white-logo {
      display: block;
    }
    img:not(.white-logo) {
      display: none;
    }
  }
  /* @include media-breakpoint-down(sm) {
    opacity: 0;
    visibility: hidden;
  } */
}
.navbar-light .navbar-toggler {
  color: #fff;
}
.app-header-navbar {
  padding: 8px 24px;
}
.app-header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 0; */
}
.app-header.active-header {
  z-index: 999;
  overflow-y: scroll;
  background: #fff;
  &.home {
    color: #000;
  }
}
.show-header {
  opacity: 1 !important;
  visibility: visible !important;
}
</style>
