<template>
  <div>
    <ValidationObserver v-slot="{ handleSubmit }" class="['d-md-block', { 'd-block': isModal, 'd-none': !isModal }]">
      <b-form @submit.prevent="handleSubmit(LoadOfferDetails)">
        <b-overlay :show="loading">
          <div :class="[
            'property-bid send-offer-form',
            { 'long-term-offer': longTerm },
          ]">
            <div>
              <!-- ## PROPERTY PRICE  -->
              <h3 class="bid-title text-left">{{ $t('property.bidTitle') }}</h3>

              <!-- ## OFFER PRICE -->
              <div class="offer-section">

                <div class="bid-price-input">
                  <label>Offer Price</label>
                  <AppInput :validation-name="$t('common.offer')" validation-rules="required|min_value:1"
                    v-model="form.budget" type="number" :min="1" :max="999999" :autofocus="true" :postfix="true"
                    maxlength="6"></AppInput>
                  <strong class="price-input-name">{{ $t('common.egp')
                    }}{{
                    longTerm
                    ? '/' + $t('common.month')
                    : '/' + $t('common.night2')
                    }}
                  </strong>
                </div>
              </div>

              <!-- ## Duration MONTHLY  -->
              <div v-if="longTerm" class="offer-section">
                <div class="offer-section-title">
                  {{ $t('common.duration') }}
                </div>

                <div class="offer-section-body duration">
                  <CounterInput v-model="form.duration" :validation-name="$t('common.duration')"
                    validation-rules="required|min_value:1" :min="1" />
                  <label class="counter-input-label">
                    {{ $t('common.month') }}
                  </label>
                </div>
              </div>

              <!-- ## TENURE DAILY  -->
              <div class="offer-period-section row" v-if="shortTerm">
                <b-col md="6" class="col">
                  <div>
                    <label class="date-input-label" :for="`datepicker-${propertyId}`">
                      <svg-icon name="calendar" class="arrow-custom"></svg-icon>
                    </label>
                    <ValidationProvider rules="required" :name="$t('common.checkIn')" v-slot="vContext">
                      <b-form-datepicker :date-format-options="dateFormatOptions" :id="`datepicker-${propertyId}`"
                        :min="currentDate" v-model="form.move_in_date" @input="changeDate()"
                        :placeholder="$t('common.checkIn')" today-button reset-button close-button locale="en"
                        aria-describedby="inputcheck-in-feedback" :state="getValidationState(vContext)">
                        <template v-slot:button-content></template>
                      </b-form-datepicker>
                      <transition name="slide">
                        <b-form-invalid-feedback id="inputcheck-in-feedback" class="error-field">{{
                          !vContext.valid && $t('common.required')
                          }}</b-form-invalid-feedback>
                      </transition>
                    </ValidationProvider>
                  </div>

                </b-col>
                <b-col md="6" class="col">
                  <div>
                    <label class="date-input-label" :for="`datepicker-${propertyId}`">
                      <svg-icon name="calendar" class="arrow-custom"></svg-icon>
                    </label>
                    <ValidationProvider rules="required" :name="$t('common.checkOut')" v-slot="vContext">
                      <b-form-datepicker :date-format-options="dateFormatOptions" :id="`datepicker1-${propertyId}`"
                        :min="minMoveOut" v-model="form.move_out_date" @input="changeDate()"
                        :placeholder="$t('common.checkOut')" today-button reset-button close-button locale="en"
                        aria-describedby="inputcheck-out-feedback" :state="getValidationState(vContext)">
                        <template v-slot:button-content></template>
                      </b-form-datepicker>
                      <transition name="slide">
                        <b-form-invalid-feedback id="inputcheck-out-feedback" class="error-field">{{
                          !vContext.valid && $t('common.required')
                          }}</b-form-invalid-feedback>
                      </transition>
                    </ValidationProvider>
                  </div>
                </b-col>
              </div>

              <!-- ## Guests  -->
              <b-col md="12" class="guests p-0 pt-3 pb-3 alignment">
                <div>
                  <div v-b-toggle="`guests-button-${propertyId}`" data-toggle="collapse"
                       :data-target="`#guests-button-${propertyId}`" class="guests-button">
                    {{ $t('common.guests') }} <br />
                    {{ form.num_adult }} {{ $t('common.adult') }} &
                    {{ form.num_children }}
                    {{ $t('common.children') }}
                  </div>
                  <b-collapse :id="`guests-button-${propertyId}`" class="mt-2">
                    <div class="guests-button-inside">
                      <div class="guests-button-inside-child">
                        <div class="guest-name">{{ $t('common.adult') }}</div>
                        <div class="guest-count">
                          <CounterInput v-model="form.num_adult" :validation-name="$t('common.adult')"
                                        validation-rules="required" :min="1" :value="1" />
                        </div>
                        <hr />
                      </div>
                      <div class="guests-button-inside-child">
                        <div class="guest-name">
                          {{ $t('common.children') }}
                        </div>
                        <div class="guest-count">
                          <CounterInput v-model="form.num_children" :validation-name="$t('common.children')"
                                        validation-rules="required" />
                        </div>
                      </div>
                    </div>
                  </b-collapse>
                </div>
              </b-col>

              <!-- ## FLEXIBLITY  -->
              <b-col md="12" class="p-0 is_flexible">
                <b-form-checkbox :id="`flexible-${propertyId}`" v-model="form.is_date_flexible" name="flexible"
                  @input="is_flexible">
                  {{ $t('common.datesAreFlexible') }}
                </b-form-checkbox>
              </b-col>

              <hr />
              <!-- ## TOTAL OFFER PRICE -->
              <b-col md="12" class="p-0 pt-2 alignment">
                <div v-if="form.duration > 0" class="sub-total-info">
                  <div class="total-price">
                    <div>
                      {{ $t('common.price') }}
                      {{
                      longTerm
                      ? ' per 1 ' + $t('common.month')
                      : ' per 1 ' + $t('common.night2')
                      }}
                    </div>
                    <div>{{ totalOffer }} {{ $t('common.egp') }}</div>
                  </div>
                </div>
              </b-col>

              <!-- ## SEND OFFER BUTTON -->
              <b-col md="12" class="p-0">
                <div class="p_0" style="margin-top: 15px">
                  <b-button no-close-on-esc no-close-on-backdrop type="submit" variant="primary"
                    class="btn btn-primary send-offer-submit">
                    {{ $t('common.sendOffer') }}
                  </b-button>
                </div>
              </b-col>
            </div>
          </div>
        </b-overlay>
      </b-form>
    </ValidationObserver>

    <!--## START MOBILE VIEW -->

    <div id="send-offer-mobile-view" :class="[
      'property-bid send-offer-form d-md-none mt-3 ',
      { 'long-term-offer': longTerm, 'd-none': isModal },
    ]">
      <b-row class="m-0 align-items-center">
        <!-- ## PROPERTY PRICE  -->
        <b-col sm="7" cols="7" class="p-0">
          <div class="asking-price">
            <span>
              <span class="price-currency">
                {{ longTerm ? propertyPrice : propertyDailyPrice }}
                {{ $t('common.egp') }}
              </span>
              <span class="period">
                {{
                longTerm ? '/' + $t('common.month') : '/' + $t('common.night')
                }}
              </span>
            </span>
          </div>

          <div class="mt-2">
            <StarRateBox :rate="propertyRate" :reviews-count="propertyReviewsCount" />
          </div>
        </b-col>

        <!-- ## SEND OFFER BUTTON -->
        <b-col sm="5" cols="5" class="p-0 justify-content-end flex-row d-flex">
          <b-button class="btn-send-offer-sm" variant="primary" @click="handleOffer">{{ $t('property.sendOffer') }}
          </b-button>
        </b-col>
      </b-row>
    </div>
    <!--## END MOBILE VIEW -->
  </div>
</template>

<script>
import CounterInput from '@/components/shared/CounterInput'
import StarRateBox from '@/components/shared/StarRateBox.vue'
import $axios from '@/helpers/axios-port'
import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('PropertyModule')

export default {
  name: 'PropertyBid',
  props: {
    budget: {
      type: Number,
    },
    propertyPrice: {
      type: Number,
    },
    propertyDailyPrice: {
      type: Number,
    },
    propertyRate: {
      type: Number,
    },
    propertyReviewsCount: {
      type: Number,
    },
    propertySecurityDeposite: {
      type: Number,
    },
    propertyName: {
      type: String,
    },
    propertyTerm: {
      type: Number | String,
    },
    propertyId: {
      type: String | Number,
    },
    isModal: {
      type: Boolean,
    },
  },
  components: {
    CounterInput,
    StarRateBox,
  },
  data() {
    return {
      windowTop: 0,
      loading: false,
      dateFormatOptions: {
        year: 'numeric',
        month: 'numeric',
        day: 'numeric',
      },
      currentDate: new Date().toJSON().slice(0, 10).replace(/-/g, '-'),
      offerInfo: {
        monthly_price: this.propertyPrice,
        daily_price: this.propertyDailyPrice,
        property_security_deposit: this.propertySecurityDeposite,
        property_rating: this.propertyRate,
        property_rating_count: this.propertyReviewsCount,
        property_name: this.propertyName,
        term: this.propertyTerm,
      },
      form: {
        property_id: this.propertyId,
        budget:
          this.propertyTerm == 0 ? this.propertyDailyPrice : this.propertyPrice,
        duration: 1,
        term: this.propertyTerm,
        move_in_date: '',
        move_out_date: '',
        is_date_flexible: 0,
        num_adult: 1,
        num_children: 0,
        num_infants: 0,
      },
    }
  },

  beforeDestroy() { },
  computed: {
    ...mapState(['showWarningToaster', 'successModalVisibility']),
    totalOffer() {
      return this.form.budget * this.form.duration
    },
    totalPrice() {
      let price = this.propertyDailyPrice

      if (this.longTerm) {
        price = this.propertyPrice
      }
      let sum = price * this.form.duration
      return sum
    },
    term() {
      return this.propertyTerm
    },
    longTerm() {
      return this.term == 1
    },
    shortTerm() {
      return this.term == 0
    },
    price() {
      return this.longTerm ? this.propertyPrice : this.propertyDailyPrice
    },
    minMoveOut() {
      if (this.form.move_in_date == '') {
        return this.currentDate
      } else {
        this.form.move_out_date = ''
        return this.form.move_in_date
      }
    },
    stepValue() {
      let value = (5 / 100) * this.form.budget
      let step = 5
      if (value > 5) {
        step = Math.round(value / 10) * 10
      }
      return step
    },
  },
  mounted() {
    this.form.price = this.price
    // this.form.budget = 0
  },
  watch: {
    budget: {
      handler(price) {
        if (price) {
          this.form.budget = price
        } else {
          this.form.budget = ''
        }
      },
      immediate: true,
    },
    successModalVisibility(newVal) {
      if (newVal) {
        this.showSuccessModal()
        this.hideSendOfferModal()
        this.hideConfirmationModal()
      } else {
        this.hideSuccessModal()
      }
    },

    price() {
      return this.longTerm ? this.propertyPrice : this.propertyDailyPrice
    },
  },
  methods: {
    ...mapActions([
      'showConfirmationModal',
      'hideSendOfferModal',
      'hideSuccessModal',
      'showSuccessModal',
      'hideConfirmationModal',
      'showSendOfferModal',
    ]),
    onScroll() {
      const offerCard = document.getElementById('send-offer-mobile-view')
      this.windowTop = window.top.scrollY
      if (this.windowTop >= 650) {
        offerCard && offerCard.classList.add('fixed-offer-card')
      } else {
        offerCard && offerCard.classList.remove('fixed-offer-card')
      }
    },

    LoadOfferDetails() {
      this.loading = false
      let data = {
        confirmData: {
          form: this.form,
          offerInfo: this.offerInfo,
          totalOffer: this.totalOffer,
        },
      }
      this.showConfirmationModal(data)
    },
    changeDate() {
      if (this.form.move_out_date != '' && this.form.move_in_date != '') {
        let date1 = new Date(this.form.move_in_date)
        let date2 = new Date(this.form.move_out_date)
        if (date2 > date1) {
          let Difference_In_Time = date2.getTime() - date1.getTime()
          // To calculate the no. of days between two dates
          let Difference_In_Days = Difference_In_Time / (1000 * 3600 * 24)
          this.form.duration = parseInt(Difference_In_Days)
        } else {
          this.form.duration = 1
        }
      }
    },
    getValidationState(vContext) {
      return vContext.errors[0] ? false : vContext.valid ? true : null
    },
    is_flexible(checked) {
      this.form.is_date_flexible = checked ? 1 : 0
    },
    handleOffer() {
      const offerCard = document.getElementById('send-offer-mobile-view')
      offerCard.classList.remove('fixed-offer-card')
      //this.showSendOfferModal({ offerData: this.offerInfo })

      if (!this.user) {
        alert('login Please')
      } else {
        $axios
          .$post('/user/sendOffer', this.offerInfo)
          .then(() => {
            this.showSuccessModal()
          })
          .catch((error) => {
            if (error.response && error.response.data.original) {
              let errorMessage = error.response.data.original.Data.message
              this._vm.$bvToast.toast(`${errorMessage} `, {
                title: 'Error',
                autoHideDelay: 5000,
                variant: 'danger',
                appendToast: true,
              })
            }
          })
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.fixed-offer-card {
  position: fixed;
  bottom: 5px;
  z-index: 2;
  background: #fff;
  left: 15px;
  right: 15px;
}

#send-offer-mobile-view {
  z-index: 9999;
  padding: 18px 20px;
  border-radius: 7px;
  // position: absolute;
  // bottom: 0;
  // left: 0;
  // right: 0;
  background-color: white;
  width: 100%;
  z-index: 0;
  margin-bottom: 30px;
  // transform: translateY(100px);
}

.send-offer-form {
  padding: 25px 20px;
  border-radius: 12px;
  box-shadow: 0 8px 15px 0 $grayLightTransparentColor;
  border: 1px solid $grayLightColor;
  font-size: 14px;

  .asking-price {
    display: flex;
    flex-direction: row;
    align-items: center;
    line-height: 1;
    .price-currency {
      font-size: 22px;
      @include media-breakpoint-down(sm) {
        font-size: 18px;
      }
      color: black;
      font-weight: 600;
    }

    .period {
      font-size: 18px;
      @include media-breakpoint-down(sm) {
        font-size: 14px;
      }
    }
  }

  .rating-box {
    cursor: pointer;
  }

  .security-deposite {
    @extend .asking-price;
    color: $grayLightColor;
    font-size: 14px;
    margin-top: 8px;
  }

  .offer-section {
    display: flex;
    flex-direction: column;
  }

  .btn-send-offer-sm {
    width: 120px;
    height: 44px;
    padding: 13px 20px;
    border-radius: 5px;
    background-color: $mainColorLight;
    border-color: $mainColorLight;
    font-size: 14px;
    line-height: 1;
  }

  &.long-term-offer .duration {
    &.offer-section-body {
      display: flex;
    }

    ::v-deep() {
      .counter-input-contianer {
        min-width: 240px;
        max-width: 240px;
      }
    }

    .counter-input-label {
      background-color: $grayBackgroundColor;
      line-height: 1;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      font-weight: 400;
      padding: 10px 12px;
      margin: 0;

      // [dir='ltr'] & {
      //   border-left: 1px solid $grayLightColor;
      // }

      // [dir='rtl'] & {
      //   border-right: 1px solid $grayLightColor;
      // }
    }
  }

  .is_flexible {
    [dir='rtl'] & {
      text-align: right;
    }
  }

  ::v-deep() {
    .counter-input-contianer {
      width: 100%;
      padding: 10px 12px;
      margin: 0;
    }
  }

  .offer-section .offer-section-title {
    font-size: 14px;
    margin-bottom: 5px;
  }

  .offer-section .offer-section-body {
    border: 1px solid $grayLightColor;
    border-radius: 5px;
    overflow: hidden;
  }

  .offer-period-section {
    display: flex;
    padding: 0;
  }

  ::v-deep() .custom-checkbox{
    input{
      width: 18px;
      height: 19px;
      background: #FFFFFF;
      border: 1px solid #C4C4C4;
    }
    label{
      font-weight: 600;
      font-size: 16px;
      line-height: 24px;
      color: #000000;
    }
  }
  .offer-period-section .col {
    width: 50%;
    position: relative;
    padding: 10px;
    border: none !important;
  }

  .offer-period-section .col:first-child {
    border-right-color: $grayLightColor;
  }

  .date-input-label {
    font-size: 14px;
    cursor: pointer;
    margin-bottom: 0;
    display: flex;
    width: 100%;
    justify-content: space-between;
    svg{
      position: absolute;
      right: 15px;
      top: 15px;
      z-index: 2;
    }
  }

  .offer-period-section .col:first-child {
    [dir='rtl'] & {
      border-left: 1px solid $grayLightColor;
    }

    [dir='ltr'] & {
      border-right: 1px solid $grayLightColor;
    }
  }

  .offer-section-title {
    [dir='rtl'] & {
      text-align: right !important;
    }
  }

  ::v-deep() {
    .b-form-datepicker>button:first-child {
      padding: 0px;
    }

    .b-form-btn-label-control.form-control {
      border: 0 !important;

      .form-control {
        font-size: 12px !important;
        padding: 0.375rem 0;
      }
    }
  }

  .offer-period-section #datepicker,
  .offer-period-section #datepicker1 {
    height: auto;
    border: none;
    padding: 0;
    margin: 0;
    font-size: 12px !important;
    font-weight: 400 !important;
    color: $secondaryTextColor;
    line-height: 1;
    display: none;
  }

  .guests-button {
    border: none !important;
    background: linear-gradient(0deg, rgba(38, 45, 61, 0.04), rgba(38, 45, 61, 0.04)), #FFFFFF;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
  }

  .guests-button-inside {
    display: flex;
    flex-direction: column;
    padding: 0px 10px;
    border-radius: 5px;
  }

  .guests-button-inside-child {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    align-items: center;
  }

  .guest-name {
    width: 50%;
  }

  .guest-count {
    align-items: center;
    width: 50%;
  }

  .total-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    align-items: center;
    color: #48535B;
  }

  .total-offer {
    @extend .total-price;
    font-weight: bold;
  }

  .send-offer-submit {
    display: block;
    width: 100%;
    padding: 12px;
    background: #0078FF;
    border-color: #0078FF;
    border-radius: 8px;
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    color: #FFFFFF;

  }

  .rate-content {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
  }
}

.bid-title {
  font-weight: bold;
  text-align: center;
  font-size: 20px;
}

.bid-subtitle {
  font-size: 12px;
  color: #828282;

  .bid-price {
    font-weight: bold;
    color: #000;
  }
}
</style>
<style lang="scss">
#success-modal {
  &.modal {
    display: flex !important;
    height: 100%;
  }

  .modal-header {
    border: none !important;
    padding-bottom: 0;
  }

  .modal-body {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;

    svg {
      width: 88px;
      height: 88px;
    }
  }

  .modal-dialog {
    max-width: 551px;
    margin: auto;
    border-radius: 5px;
  }

  .close-button {
    background: none;
    border: none;
    margin: 0px;
    padding: 0px;
    line-height: 1;

    svg {
      width: 24px;
      height: 24px;
    }
  }
}
.price-input-name{
  top: 45px !important;
}
.bid-price-input {
  position: relative;
  label{
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: #262D3D;
  }

  span.input-field {
    fieldset.form-group {
      div {
        input {
          height: 96px;
          text-align: center;
          font-size: 40px;
          background-image: none !important;
          padding-right: 20px !important;
          padding-left: 20px !important;
        }
      }
    }
  }
}
</style>
