<template>
  <ValidationProvider
    v-slot="vContext"
    :rules="validationRules"
    :name="validationName"
    class="input-field d-inline-block w-100"
  >
    <b-form-group
      :class="[
        `${inputId}-wrapper mb-2`,
        vContext.errors[0] ? 'group-is-invalid' : '',
        getValidationState(vContext) ? 'group-is-valid' : '',
      ]"
    >
      <template
        v-if="label"
        #label
        :class="labelClasses"
        :for="inputId ? inputId : ''"
        >{{ label }}
        <sup v-if="isRequired" style="color: red">*</sup>
      </template>
      <!-- inRange ? 'is-valid' : 'is-invalid', -->
      <b-form-input
        class="form-input appInput"
        :class="[inRange !== 'empty' ? inRange ? 'is-valid' : 'is-invalid' : '', $attrs.type === 'password' ? 'no-background' : '']"
        :id="inputId"
        ref="inputHandler"
        :placeholder="placeholder ? placeholder : label"
        :state="state ? getValidationState(vContext) : null"
        :aria-describedby="feedbackId"
        :autocomplete="autocomplete ? autocomplete : 'on'"
        v-bind="$attrs"
        v-on="$listeners"
        @focus="onFocusHandler"
        @blur="onBlurHandler"
        :autofocus="autofocus"
        v-bind:type="
          $attrs.type === 'password'
            ? showPass
              ? 'text'
              : 'password'
            : $attrs.type
        "
      ></b-form-input>

      <div :class="['show-pass-container', dir === 'rtl' ? 'showPassAr' : 'showPassEn']" v-if="$attrs.type === 'password'">
        <svg-icon
          v-if="!showPass"
          name="eye-enable"
          viewBox="0 0 28 28"
          v-on:click="showPass = !showPass"
        />
        <svg-icon
          v-else
          name="eye-disable"
          viewBox="0 0 28 28"
          v-on:click="showPass = !showPass"
        />
      </div>

      <div
        v-if="postfix"
        class="input-postfix"
        v-bind:style="
          $i18n.locale === 'en'
            ? $attrs.value.length < 7
              ? 'left:calc( 50% + ' + $attrs.value.length * 7 + 'px );'
              : $attrs.value.length
              ? 'right: 15px;left:auto;'
              : ''
            : $i18n.locale === 'ar'
            ? $attrs.value.length < 7
              ? 'right:calc( 50% + ' + $attrs.value.length * 7 + 'px );'
              : $attrs.value.length
              ? 'left: 60px;right:auto;'
              : ''
            : ''
        "
      >
        {{ postfixText }}
      </div>
      <b-form-text
        id="input-live-help"
        v-if="hintMessage && !vContext.errors[0]"
        >{{ hintMessage }}</b-form-text
      >
      <transition name="slide">
        <b-form-invalid-feedback :id="feedbackId" class="error-field">
          {{ inRange !== false ? '' : dir === 'ltr' ? `Out of Range, Range from ${min}% to ${max}%` : ` ,   ${min}%  ${max}%` }}</b-form-invalid-feedback>
      </transition>
    </b-form-group>
  </ValidationProvider>
</template>
<script>
export default {
  name: 'AppInput',
  props: {
    validationName: {
      type: String,
      required: false,
      default: 'text',
    },
    validationRules: {
      type: String,
      default: '',
    },
    vid: {
      type: String,
      default: '',
    },
    inputId: {
      type: String,
      default: '',
    },
    hintMessage: {
      type: String,
      default: '',
    },
    label: {
      type: String,
      default: '',
    },
    placeholder: {
      type: String,
      default: '',
    },
    autocomplete: {
      type: String,
      default: 'on',
    },
    state: {
      type: Boolean,
      default: true,
    },
    isRequired: {
      type: Boolean,
      default: false,
    },
    autofocus: {
      type: Boolean,
      default: false,
    },
    postfix: {
      type: Boolean,
      default: false,
    },
    postfixText: {
      type: String,
      default: '',
    },
    min: {
      type: Number,
      required: false,
      default : 0
    },
    max: {
      type: Number,
      required: false
    }
  },
  data() {
    return {
      isEmpty: true,
      showPass: false,
    }
  },
  computed: {
    feedbackId() {
      return 'input' + this.validationName.replace(/ /g, '-') + '-feedback'
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
    labelClasses() {
      return [
        !this.isEmpty || this.$attrs.value ? 'active' : '',
        this.errors ? 'color-danger' : '',
      ]
    },
    inRange(){
      let min = parseInt(this.min);
      let max = parseInt(this.max);
      let currentVal = parseInt(this.$attrs.value);
      if(!!min && !!max){
        if(!isNaN(currentVal)){
          if(currentVal >= min && currentVal <= max){
            return true
          }else{
            return false
          }
        }else{
          return "empty"
        }
      }else{
        return true
      }
    }
  },
  methods: {
    checkInput() {
      if (String(this.$attrs.value).length === 0) {
        this.isEmpty = true
      } else {
        this.isEmpty = false
      }
    },
    getValidationState(vContext) {
      return vContext.errors[0]
        ? false
        : this.$attrs.value && String(this.$attrs.value).length !== 0
        ? vContext.valid
          ? true
          : null
        : null
    },
    onFocusHandler() {
      this.isEmpty = false
    },
    onBlurHandler() {
      this.checkInput() || (this.isEmpty = true)
    },
    removeValidationClasses(){
      let theInput = document.querySelector(".appInput");
      if(theInput.className.indexOf('is-invalid') >= 0 || theInput.className.indexOf('is-valid') >= 0){
        theInput.classList.remove("is-valid", "is-invalid")
      }
    }
  },
}
</script>

<style lang="scss" scoped>
input{
  padding: 12px;
  height: 48px;
  background: #FFFFFF;
  border-radius: 8px;
  margin: 4px 0;
}
.input-field input.form-control:not(.is-invalid) {
  border-radius: 4px;
  border: 1px solid $grayLightColor;
}

.input-postfix {
  position: absolute;
  font-size: 20px;
  font-weight: bold;
  top: 50%;
  transform: translate(5, -50%);
  left: calc(50% + 10px);
  background-color: white;
  z-index: 6;
  [dir='rtl'] & {
    transform: translate(calc(-100% - 20px), -50%);
    left: 50%;
  }
}
.show-pass-container {
  cursor: pointer;
  transform: translateY(calc(-100% - 12px));
  &.showPassEn {
    float: right;
    margin-right: 5px;
  }
  &.showPassAr {
    float: left;
    margin-left: 10px;
  }
  svg {
    width: 28px;
    height: 28px;
  }
}
.input-field input.form-control.no-background {
  background-image: none !important;
}
</style>
