<template>
  <b-button :id="id" :disabled="loading || disabled" v-bind="$attrs" v-on="$listeners">
    <b-spinner v-if="loading" small></b-spinner> <span v-else>{{ label }}</span
    ><slot></slot>
  </b-button>
</template>
<script>
export default {
  name: 'AppButton',
  props: {
    disabled: {
      type: Boolean,
      required: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: '',
    },
    id:{
      type: String
    }
  },
}
</script>
