let axios = null

export function setClient(nuxtAxios) {
  axios = nuxtAxios
  updateService()
}

// Request helpers
const reqMethods = [
  '$request',
  '$delete',
  '$get',
  '$head',
  '$options', // url, config
  '$post',
  '$put',
  '$patch', // url, data, config
]

let axiosPort = {}

const updateService = () => {
  reqMethods.forEach((method) => {
    axiosPort[method] = function () {
      if (!axios) throw new Error('apiClient not installed')
      return axios[method].apply(null, arguments)
    }
  })
}

export default axiosPort
