<template>
  <b-img-lazy v-if="src" :src="computedSrc" :blank="true" blank-color="#dbebff" :offset="200" v-bind="$attrs">
  </b-img-lazy>
</template>

<script>
import { generateServerImageUrl } from '@/helpers/images'
export default {
  name: 'AppImage',
  inheritAttrs: false,
  props: {
    src: {
      type: String,
      required: true,
    },
    width: {
      type: Number,
      required: false,
      default: 400,
    },
    height: {
      type: Number,
      required: false,
      default: 400,
    },
    withoutBaseUrl: {
      type: Boolean,
      required: false,
      default: false,
    },
    quality: {
      type: Number,
      required: false,
      default: 60,
      validator(value) {
        return value > 0 && value <= 100
      },
    },
    withoutCompression: {
      type: Boolean,
      required: false,
      default: false,
    },
  },
  computed: {
    computedSrc() {
      if (this.withoutCompression) {
        if (this.src.indexOf("Amenities/") >= 0) {
          if(this.src.startsWith('/')){
            return `${this.$config.AMENITIES_BASE_URL}${this.src}`
          }else{
            return `${this.$config.AMENITIES_BASE_URL}/${this.src}`
          }
        }
        return `${this.$config.APP_DOMAIN_BASE_URL}${this.src}`
      }
      const imageUrl = generateServerImageUrl({
        $config: this.$config,
        url: this.src,
        width: this.width,
        height: this.height,
        quality: this.quality,
      })

      return this.withoutBaseUrl ? this.src : imageUrl
    },
  },
}
</script>
