<template>
  <no-ssr>
    <vue-star-rating
      :rtl="isRTL"
      :rating="value ? Number(value) : 0"
      :increment="increment"
      :show-rating="showRating"
      :star-size="starSize"
      inactive-color="#787f81"
      :border-width="0"
      active-color="#FFCC00"
      :read-only="readOnly"
      @rating-selected="$emit('update:value', $event)"
      :star-points="[
        23, 2, 14, 17, 0, 19, 10, 34, 7, 50, 23, 43, 38, 50, 36, 34, 46, 19, 31,
        17,
      ]"
    ></vue-star-rating>
  </no-ssr>
</template>

<script>
export default {
  name: 'app-star-rating',
  props: {
    readOnly: {
      type: Boolean,
      default: true,
    },
    value: {
      type: Number,
      default: 0,
    },
    increment: {
      type: Number,
      default: 1,
    },
    showRating: {
      type: Boolean,
      default: false,
    },
    starSize: {
      type: Number,
      default: 15,
    },
  },
  computed: {
    isRTL() {
      return this.$store.getters['CoreModule/isRTL']
    },
  },
}
</script>

<style lang="scss" scoped>
::v-deep() .vue-star-rating {
  display: inline-flex;
}
::v-deep() span.vue-star-rating-star {
  margin-right: 5px !important;
  line-height: 1;
  .vue-star-rating-rtl &:first-child {
    margin-right: 0 !important;
  }
}
::v-deep() {
  .sr-only {
    left: 0 !important;
  }
}
</style>
