<template>
  <div :class="['separator', { 'double-space': doubleSpace }]">
    <span v-if="text" class="separator-text">{{ text }}</span>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
      default: '',
    },
    doubleSpace: {
      type: Boolean,
    },
  },
}
</script>

<style lang="scss" scoped>
.separator {
  width: 100%;
  display: flex;
  padding: 15px 0;
  align-items: center;
  color: $secondaryTextColor;
  font-size: 12px;
  line-height: 1;
  &.double-space {
    padding: 30px 0;
  }
  .separator-text {
    padding: 0 10px;
    text-transform: capitalize;
  }
  &:before,
  &:after {
    content: '';
    display: inline-block;
    width: 50%;
    border-top: 1px solid $grayTransparentColor;
  }
}
</style>
