import { authenticationToken } from '@/helpers/constants/auth'

export default ({ app: { $cookies, i18n }, redirect }) => {
  const selectedLocaleCode = i18n.localeProperties.code

  if (!$cookies.get(authenticationToken))
    return redirect(
      `${
        i18n.defaultLocale === selectedLocaleCode
          ? ''
          : `/${selectedLocaleCode}`
      }/`
    )
  return
}
