<template>
  <div v-if="!showTour">
    <v-tour
      :name="tour"
      :steps="steps"
      :options="options"
      :callbacks="callbacks"
    ></v-tour>
  </div>
</template>

<script>
import cookie from 'js-cookie'

export default {
  name: 'CustomTour',
  props: {
    steps: {
      type: Array,
      default: () => [],
    },
    tour: {
      type: String,
      default: 'home',
    },
    cookie_name: {
      type: String,
      default: '',
    },
  },
  computed: {
    showTour() {
      return (
        cookie.get(this.cookie_name) && JSON.parse(cookie.get(this.cookie_name))
      )
    },
    options() {
      return {
        labels: {
          buttonSkip: this.$t('common.skip'),
          buttonPrevious: this.$t('common.previous'),
          buttonNext: this.$t('common.next'),
          buttonStop: this.$t('common.finish'),
        },
      }
    },
    callbacks() {
      return {
        onSkip: this.onSkip,
        onFinish: this.onFinish,
      }
    },
  },
  methods: {
    onSkip() {
      this.handleEndTour()
    },
    onFinish() {
      this.handleEndTour()
    },
    handleEndTour() {
      if (!cookie.get(this.cookie_name))
        cookie.set(this.cookie_name, JSON.stringify(true))
    },
  },
  mounted() {
    this.$tours[this.tour] && this.$tours[this.tour].start()
  },
}
</script>
