import { setClient } from '@/helpers/axios-port'
import { authenticationToken } from '@/helpers/constants/auth'

export default ({ $axios, app: { $cookies }, store: { state } }) => {
  $axios.onRequest((config) => {
    const authToken = state.AuthModule.token || $cookies.get(authenticationToken)
    let headers = {
      'X-localization': $cookies.get("i18n_redirected")
    }
    if (authToken) {
      headers.Authorization = `Bearer ${authToken}`
    }
    config.headers.common = {
      ...config.headers.common,
      ...headers,
    }
  })
  $axios.onResponse((config) => {})

  $axios.onError((error) => {
    return Promise.reject(error)
  })

  setClient($axios)
}
