import api from './api'
import $axios from '@/helpers/axios-port'

export default {
  login({ commit, dispatch }, { payload, needReload = false }) {
    commit('startAuthLoading')
    return api
      .login(payload)
      .then((res) => {
        commit('setUserToken', res.Data.token)
        commit('setServerErrors', null)
        commit('setSuccessResponse', res.Data.message)

        sessionStorage.setItem('user', JSON.stringify(res.Data))
        if (needReload) {
          setTimeout(() => {
            location.reload()
          }, 1500)
        } else {
          return dispatch(
            'UserModule/getUserProfile',
            { ...res.Data },
            { root: true }
          )
        }
        this.$eventBus.$emit('on-user-loggedIn')

        return res.Data
      })
      .catch((error) => {
        commit(
          'setServerErrors',
          error.response.data.Data
            ? [{ message: error.response.data.Data.message }]
            : error.response.data.errors
        )
        commit('setSuccessResponse', null)
      })
      .finally(() => {
        commit('endAuthLoading')
      })
  },
  createAccount({ commit, dispatch }, { payload, needReload = false }) {
    commit('startAuthLoading')

    return api
      .createAccount(payload)
      .then((res) => {
        if (res) {
          commit('setUserToken', res.Data.token)
          commit('setServerErrors', null)
          commit('setSuccessResponse', res.Data.message)
          if(payload.type === 'Broker'){
            $axios.$post('/user/corprate/changeType')
          }
          if (needReload) {
            setTimeout(() => {
              location.reload()
            }, 1500)
          } else {
            return dispatch(
              'UserModule/getUserProfile',
              { ...res.Data },
              { root: true }
            )
          }
        }
        this.$eventBus.$emit('on-user-loggedIn')

        return true
      })
      .catch((error) => {
        if (error.response.data.errors) {
          commit('setServerErrors', error.response.data.errors)
        } else {
          commit('setServerErrors', error.response.data.Data.message)
        }
        commit('setSuccessResponse', null)
      })
      .finally(() => {
        commit('endAuthLoading')
      })
  },
  forgotPassword({ commit }, payload) {
    commit('startAuthLoading')
    return api
      .forgotPassword(payload)
      .then((response) => {
        if (response) {
          commit('setServerErrors', null)
          commit('setSuccessResponse', response.Data.message)
        }
      })
      .catch((error) => {
        if (error.response.data.Data) {
          commit('setServerErrors', error.response.data.Data.message)
        } else {
          commit('setServerErrors', error.response.data.message)
          commit('setSuccessResponse', null)
        }
      })
      .finally(() => {
        commit('endAuthLoading')
      })
  },
  resetPassword({ commit }, payload) {
    commit('startAuthLoading')
    return api
      .resetPassword(payload)
      .then((response) => {
        commit('setServerErrors', null)
        commit('setSuccessResponse', response.Data.message)
      })
      .catch((error) => {
        commit('setServerErrors', error.response.data.Data)
        commit('setSuccessResponse', null)
      })
      .finally(() => {
        commit('endAuthLoading')
      })
  },
  resetResponse({ commit }) {
    commit('setServerErrors', null)
    commit('setSuccessResponse', null)
  },
  showAuthModal({ commit }, payload) {
    commit('updateAuthModalVisibility', {

      callbackAction: payload.callbackAction,
      visibility: true,
    })
  },
  hideAuthModal({ commit }) {
    commit('updateAuthModalVisibility', {
      visibility: false,
      callbackAction: {
        name: null,
        paylaod: null,
      },
    })
  },
  logout({ commit }) {
    commit('removeUserToken')
  },
  setToken({ commit }, token) {
    commit('setUserToken', token)
  },
}
