<template>
  <ValidationObserver v-slot="{ handleSubmit }">
    <form @submit.prevent="handleSubmit(submit)">
      <div class="form-header">
        <h5 class="form-title">{{ $t('header.login') }}</h5>
        <p class="form-description"> {{ dir === 'ltr' ? 'Login to start you rental experience' : '     ' }}</p>
      </div>
      <AppInput
        :validation-name="$t('form.email')"
        :placeholder="$t('form.email')"
        type="email"
        autoComplete="email"
        required
        validation-rules="required|email"
        v-model="form.email"
      ></AppInput>

      <AppInput
        :validation-name="$t('form.password')"
        :placeholder="$t('form.password')"
        type="password"
        autoComplete="current-password"
        required
        validation-rules="required|min:6"
        v-model="form.password"
        :hintMessage="$t('form.password_hint_message')"
      ></AppInput>

      <AppButton :id="createId" type="submit" block variant="primary" :loading="authLoading">
        {{ $t('header.login') }}
      </AppButton>
      <p class="form-message mt-3 mb-0">
        <b-button
          variant="link"
          class="p-0"
          @click="$emit('on-switch', 'ForgotPasswordForm')"
        >
          Forgot my password
        </b-button>
      </p>
      <Separator :text="$t('common.or')" />
      <SocialLogin
        @success="loginSuccess"
        :need-reload="needReload"
        :dialog="dialog"
      />
      <p class="form-message">
        {{ $t('header.createAccountMessage') }}
        <b-button
          variant="link"
          class="p-0"
          @click="$emit('on-switch', 'SignupForm')"
        >
          {{ $t('header.signup') }}
        </b-button>
      </p>
    </form>
  </ValidationObserver>
</template>

<script>
import Separator from '@/components/shared/Separator'
import SocialLogin from '@/components/shared/SocialLogin'

import { createNamespacedHelpers } from 'vuex'
const { mapState, mapActions } = createNamespacedHelpers('AuthModule')
export default {
  components: {
    Separator,
    SocialLogin,
  },
  props: {
    needReload: {
      required: true,
      type: Boolean,
    },
    dialog: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      form: {
        email: null,
        password: null,
        // types
      },
    }
  },
  computed: {
    ...mapState(['authLoading', 'successResponse']),
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
    createId(){
      let currentUrl = this.$route.name;
      if(currentUrl.indexOf('requests-post-preview') >= 0){
        return `postRequest-login`
      }else if (currentUrl.indexOf('properties-add-6') >= 0){
        return `addListing-login`
      }else if(currentUrl.indexOf('marketplace') >= 0){
        return `marketplace-login`
      }else if(currentUrl.indexOf('index') >= 0){
        return `homepage-login`
      }
    }
  },
  methods: {
    ...mapActions(['login']),
    submit() {
      this.login({ payload: this.form, needReload: this.needReload }).then(
        (data) => {
          if (data) {
            this.loginSuccess(true)
            
            this.$bvToast.toast(
              `${this.$t('form.welcome_back')} ${data ? data.full_name : ''}`,
              {
                variant: 'success',
                toaster: 'b-toaster-top-center',
                solid: true,
                bodyClass: ['toast-body'],
                headerClass: ['toast-header'],
                noCloseButton: true,
              }
            )
          }
        }
      )
    },
    loginSuccess(reqStatus) {
      setTimeout(() => {
        if (reqStatus) this.$emit('success')
      }, 1000)
      if (this.successResponse) {
        this.$emit('success')
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.form-header {
  margin-bottom: 25px;
}
.form-title {
  font-size: 18px;
}
.form-description {
  color: $secondaryTextColor;
  font-size: 12px;
  margin-top: 5px;
  margin-bottom: 0;
}
.form-message {
  display: flex;
  justify-content: center;
  button {
    border: none;
    margin: 0 10px;
  }
}
</style>
